<template>
  <m-container>
    <m-header headType="1" @search="getVal" @back="goBack"></m-header>
    <m-center :pageStatus="pageStatus">
      <div class="m-content animated fadeIn" v-cloak>
        <!-- 历史记录 -->
        <div class="s-history" v-if="history && history.length>0" v-cloak>
          <div class="wrap flex flex-between s-wrap-head p-l-30 p-r-30 p-t-30 p-b-20">
            <p class="s-left font-32 c-39">历史记录</p>
            <p class="s-right font-26" @click="deleteHistory">
              <img src="../../assets/img/common/icon_delete.png" alt="delete" />
            </p>
          </div>
          <div class="search-keywords max-h flex flex-wrap p-l-30 p-r-30">
            <p class="search-item c-66" v-for="(item,index) in history" :key="index" @click="search(item)">{{item}}</p>
          </div>
        </div>
        <!-- 热门搜索 -->
        <div class="s-hot" v-if="hotSearch" v-cloak>
          <div class="wrap flex flex-between s-wrap-head p-l-30 p-r-30 p-t-20 p-b-20">
            <p class="s-left font-32 c-39">热门搜索</p>
            <p class="s-right"></p>
          </div>
          <div class="search-keywords max-h flex flex-wrap p-l-30 p-r-30">
            <p
              class="search-item c-66"
              v-for="(hotItem,hotIndex) in hotSearch"
              :key="hotIndex"
              @click="search(hotItem)"
            >{{hotItem}}</p>
          </div>
        </div>
        <!-- 广告 -->
        <div class="s-ad" v-if="adSwiper" v-cloak>
          <div class="wrap">
            <swiper class="s-swiper p-l-30 p-r-30" :options="swiperOption">
              <swiper-slide class="swiper-slide" v-for="(item,index) in adSwiper" :key="index" v-show="item.PictureUrl">
                <a :href="item.Url" replace>
                  <img :src="item.PictureUrl" :alt="item.Name" />
                </a>
              </swiper-slide>
              <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
          </div>
        </div>
      </div>
    </m-center>
  </m-container>
</template>

<script>
//import api from '@/plus/api';
import store from "@/store";
import config from "@/plus/http/config";
export default {
  name: "index",
  data() {
    return {
      pageStatus: 0,
      host: config.host,
      hotSearch: void 0, //热门搜索
      history: void 0, //历史记录
      searchVal: "", //搜索词
      adSwiper: void 0, //广告
      swiperOption: {
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        spaceBetween: 15,
        loop: true,
        autoplay: true
      }
    };
  },
  computed: {},
  activated() {},
  mounted() {
    this.pageStatus = 2;
    this.getHotList();
    this.getAd();
  },
  methods: {
    goBack(){
      this.$router.go(-1);
    },
    //热门搜索
    async getHotList() {
      var _this = this;
      await _this.$http.search
        .getHotData()
        .then(res => {
          if (res.data.Success) {
            var data = res.data.Content;
            _this.hotSearch = data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //获取广告
    async getAd() {
      var _this = this;
      await _this.$http.search
        .getAdData()
        .then(res => {
          if (res.data.Success) {
            var data = res.data.Content;
            _this.adSwiper = data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //删除历史记录
    deleteHistory() {
      var _this = this;
      _this.history = [];
      _this.history.length = 0;
      localStorage.removeItem("searchHistory");
    },
    //删除数组里面重复的元素
    delArrElem(arr, s) {
      var i = arr.length;
      while (i--) if (arr[i] === s) arr.splice(i, 1);
      return arr;
    },
    //搜索记录
    historyList() {
      var _this = this;
      var historyList = JSON.parse(
        window.localStorage.getItem("searchHistory")
      );
      if (historyList != null && historyList) {
        _this.history = historyList.reverse();
      }
    },

    getVal(keyword) {
      var _this = this;
      _this.searchVal = keyword;
      if (_this.searchVal.indexOf("#") > -1) {
        _this.searchVal = _this.searchVal.replace(/#/g, "");
      }
      _this.search(_this.searchVal);
    },
    //搜索
    async search(val) {
      var _this = this;
      var params = {
        keyword: val
      };
      var keys = [];
      if (
        window.localStorage.getItem("searchHistory") != null &&
        window.localStorage.getItem("searchHistory")
      ) {
        keys = JSON.parse(window.localStorage.getItem("searchHistory"));
      }
      if (keys.indexOf(val) > -1) _this.delArrElem(keys, val);
      keys.push(val);
      window.localStorage.setItem("searchHistory", JSON.stringify(keys));
      _this.history = keys.reverse();
      // await _this.$http.search
      //   .getSearch(params)
      //   .then(res => {
      //     if (res.data.Success) {
      //       _this.history = keys.reverse();
      //       console.log(_this.history)
      //     }
      //   })
      //   .catch(err => {
      //     console.log(err);
      //   });
    }
  }
};
</script>

<style lang="less" scoped>
.s-wrap-head {
  .s-right {
    img {
      display: block;
      width: 0.4rem;
      height: 0.4rem;
      object-fit: contain;
    }
  }
}
.search-keywords {
  &.max-h {
    max-height: 2.4rem;
    overflow: hidden;
  }
  .search-item {
    background: #f2f2f2;
    height: 0.6rem;
    line-height: 0.6rem;
    padding: 0 0.3rem;
    font-size: 0.26rem;
    margin: 0 0.3rem 0.2rem 0;
    border-radius: 5rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
.s-swiper {
  width: 100%;
  height: 2.5rem;
  position: relative;
  margin: 0.3rem auto;
  border-radius: 0.12rem;
  overflow: hidden;
  .swiper-slide {
    img {
      width: 100%;
      height: 2.1rem;
      -o-object-fit: cover;
      object-fit: cover;
      border-radius: 0.12rem;
    }
  }
}
</style>
<style lang="css" scoped>
/* 轮播分页按钮样式修改 */
.s-swiper >>> .swiper-pagination-bullets {
  bottom: 0px;
  left: 50%;
  width: auto;
  transform: translateX(-50%);
  background: #f5f5f5;
  border-radius: 5rem;
}
.s-swiper >>> .swiper-pagination-bullet {
  margin: 0 3px;
  width: 10px;
  height: 10px;

  /* box-shadow: 0px 0px 1px 2px #fff inset; */
}
.s-swiper >>> .swiper-pagination-bullet-active {
  background: #e81717;
  width: 20px;
  border-radius: 2rem;
  /* box-shadow: 0px 0px 1px 3px rgba(0, 0, 0, 0.6) inset; */
}
</style>


