        </div>
        <div class="content-box border-bottom p-t-30 p-b-30" v-if="projectInfo && projectInfo.AestheticStandard">
          <h2 class="font-30 p-b-30 c-33">美学标准：</h2>
          <p class="font-30 c-66" v-html="projectInfo.AestheticStandard"></p>
        </div>
