            <p class="font-30 c-66">6、手术使用的材料</p>
          </div>
          <router-link to="/"><img class="block p-t-30" src="https://ubmcmm.baidustatic.com/media/v1/0f000PDISovGt4gQ0fJS46.jpg" alt=""></router-link>
        </div>
        <div class="content-box border-bottom p-t-30 p-b-30" v-if="projectInfo">
          <h2 class="font-30 p-b-30 c-33">优缺点：</h2>
          <p class="font-30 c-66 p-b-10">优点：<span class="font-30" v-html="projectInfo.OperationAdvantage"></span></p>
          <p class="font-30 c-66">缺点：<span class="font-30" v-html="projectInfo.OperationDefect"></span></p>
        </div>
        <div class="content-box border-bottom p-t-30 p-b-30" v-if="projectInfo">
          <h2 class="font-30 p-b-30 c-33">适宜人群与禁忌人群：</h2>
          <div class="wrap" v-if="projectInfo && projectInfo.ApplyPeople">
            <h3 class="font-30  p-b-20 normal c-66">适宜人群：</h3>
            <p class="font-30 c-66" v-html="projectInfo.ApplyPeople"></p>
          </div>
          <div class="p-t-20" v-if="projectInfo && projectInfo.TabooCrowd">
              <h3 class="font-30  p-b-20 normal c-66">禁忌人群：</h3>
              <p class="font-30 c-66" v-html="projectInfo.TabooCrowd"></p>
          </div>
        </div>
        <div class="content-box border-bottom p-t-30 p-b-30" v-if="projectInfo">
          <h2 class="font-30 p-b-30 c-33">注意事项：</h2>
          <div class="font-30" v-if="projectInfo.Preparation">
            <h3 class="font-30  p-b-20 normal c-66">术前准备：</h3>
            <p class="font-30 c-66" v-html="projectInfo.Preparation"></p>
          </div>
          <div class="p-t-20" v-if="projectInfo.Methods">
            <h3 class="font-30  p-b-20 normal c-66">术后恢复提示与护理方法：</h3>
            <p class="font-30 c-66" v-html="projectInfo.Methods"></p>
          </div>
        </div>
        <div class="content-box p-t-30 p-b-30" v-if="projectInfo && projectInfo.SurgicalComplications">
          <h2 class="font-30 p-b-30 c-33">副作用及风险提示：</h2>
          <p class="font-30 c-66" v-html="projectInfo.SurgicalComplications"></p>
        </div>
        <!-- 费用 -->
        <div class="cost-box" v-if="projectInfo" v-cloak>
          <div class="cost-item p-b-30" v-if="projectInfo.Price">
            <span class="cost-left font-28">费用</span>
            <div class="cost-right p-b-30">
              <p class="font-30 c-66">{{projectInfo.Price}}</p>
            </div>
          </div>
          <div class="cost-item p-b-30" v-if="projectInfo.TreatmentWay || projectInfo.Narcosis">
            <span class="cost-left font-28">方法</span>
            <div class="cost-right p-b-30">
              <p class="font-30 c-66">{{projectInfo.TreatmentWay}}</p>
              <p class="font-30 c-66">{{projectInfo.Narcosis}}</p>
            </div>
          </div>
          <div class="cost-item p-b-30" v-if="projectInfo.OperationCourse || projectInfo.Durations || projectInfo.LengthofStay">
            <span class="cost-left font-28">治疗</span>
            <div class="cost-right p-b-30">
              <p class="font-30 c-66" v-if="projectInfo.OperationCourse">{{projectInfo.OperationCourse}}治疗</p>
              <p class="font-30 c-66" v-if="projectInfo.Durations">{{projectInfo.Durations}}手术</p>
              <p class="font-30 c-66" v-if="projectInfo.LengthofStay">住院：{{projectInfo.LengthofStay}}</p>
            </div>
          </div>
          <div class="cost-item p-b-30" v-if="projectInfo.Stitches || projectInfo.Recoveries || projectInfo.SwellingTime">
            <span class="cost-left font-28">恢复</span>
            <div class="cost-right p-b-30">
              <p class="font-30 c-66" v-if="projectInfo.Stitches">{{projectInfo.Stitches}}拆线</p>
              <p class="font-30 c-66" v-if="projectInfo.Recoveries">{{projectInfo.Recoveries}}消肿</p>
              <p class="font-30 c-66" v-if="projectInfo.SwellingTime">{{projectInfo.SwellingTime}}恢复</p>
            </div>
          </div>
          <div class="cost-item p-b-30" v-if="projectInfo.DurationTime">
            <span class="cost-left font-28">效果</span>
            <div class="cost-right p-b-30">
              <p class="font-30 c-66">效果维持：{{projectInfo.DurationTime}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="bg-f5 p-b-20"></div>
      <!-- 本地医院 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30" v-if="projectInfo" v-cloak>
        <div class="box-title flex">
          <i class="icon-title"></i>
          <p class="font-34">本地医院</p>
        </div>
        <div class="wrap-box hospital-box">
          <router-link to="" tag="div" class="hospital flex flex-between border-bottom p-t-30 p-b-30">
            <img class="hospital-head" src="https://cdn-ssl.meb.com/hospital/20170518/258beb08111844eda867a40d657ae1a5240-s" alt="">
            <div class="hospital-info one-line flex flex-column flex-center flex-top">
              <p class="font-32 one-line">武侯艾米丽医疗美容门诊部</p>
              <p class="font-26 c-b2 one-line p-t-20">四川省成都市武侯区龙腾西路3号-附38号</p>
            </div>
          </router-link>
          <router-link to="" tag="div" class="hospital flex flex-between border-bottom p-t-30 p-b-30">
            <img class="hospital-head" src="https://cdn-ssl.meb.com/hospital/20170518/258beb08111844eda867a40d657ae1a5240-s" alt="">
            <div class="hospital-info one-line flex flex-column flex-center flex-top">
              <p class="font-32 one-line">武侯艾米丽医疗美容门诊部</p>
              <p class="font-26 c-b2 one-line p-t-20">四川省成都市武侯区龙腾西路3号-附38号</p>
            </div>
          </router-link>
          <router-link to="" tag="div" class="hospital flex flex-between border-bottom p-t-30 p-b-30">
            <img class="hospital-head" src="https://cdn-ssl.meb.com/hospital/20170518/258beb08111844eda867a40d657ae1a5240-s" alt="">
            <div class="hospital-info one-line flex flex-column flex-center flex-top">
              <p class="font-32 one-line">武侯艾米丽医疗美容门诊部</p>
              <p class="font-26 c-b2 one-line p-t-20">四川省成都市武侯区龙腾西路3号-附38号</p>
            </div>
          </router-link>
          <router-link to="" tag="div" class="look-more flex flex-center p-t-30">
            <img src="../../assets/img/common/more.png" alt=""><span class="font-28 c-99">获取成都更多医院</span>
          </router-link>
        </div>
      </div>
      <div class="bg-f5 p-b-20"></div>
      <!-- 您可能关注的问题 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30" v-if="projectInfo" v-cloak>
        <div class="box-title flex">
          <i class="icon-title"></i>
          <p class="font-34">您可能关注的问题</p>
        </div>
        <div class="wrap-box question-box">
          <router-link to="" tag="div" class="question border-bottom p-t-30 p-b-30">
            <p class="question-title one-line c-33 font-32">成都黑脸娃娃多少钱</p>
            <div class="question-data one-line m-t-20 font-28 flex flex-between">
              <p class="data-number one-line c-b2">
                <span class="c-meb">10</span>名专家回复<span class="p-l-10 p-r-10">·</span><span class="c-meb">898</span><span>人关注</span>
              </p>
              <span class="meb-recommend flex c-b2 font-24"><img src="../../assets/img/common/tuijian.png" alt="" />美呗推荐</span>
            </div>
          </router-link>
          <router-link to="" tag="div" class="question border-bottom p-t-30 p-b-30">
            <p class="question-title two-line c-33 font-32">成都黑脸娃娃多少钱</p>
            <p class="question-data one-line m-t-20 c-b2 font-28"><span class="c-meb">10</span>名专家回复<span class="p-l-10 p-r-10">·</span><span class="c-meb">898</span><span>人关注</span></p>
          </router-link>
          <router-link to="" tag="div" class="question border-bottom p-t-30 p-b-30">
            <p class="question-title two-line c-33 font-32">成都黑脸娃娃多少钱</p>
            <p class="question-data one-line m-t-20 c-b2 font-28"><span class="c-meb">10</span>名专家回复<span class="p-l-10 p-r-10">·</span><span class="c-meb">898</span><span>人关注</span></p>
          </router-link>
          <router-link to="" tag="div" class="question border-bottom p-t-30 p-b-30">
            <p class="question-title two-line c-33 font-32">成都黑脸娃娃多少钱</p>
            <p class="question-data one-line m-t-20 c-b2 font-28"><span class="c-meb">10</span>名专家回复<span class="p-l-10 p-r-10">·</span><span class="c-meb">898</span><span>人关注</span></p>
          </router-link>
          <router-link to="" tag="div" class="look-more flex flex-center p-t-30">
            <img src="../../assets/img/common/more.png" alt=""><span class="font-28 c-99">更多相关问题</span>
          </router-link>
        </div>
      </div>

    </m-center>
  </m-container>
</template>

<script>
//import api from '@/plus/api';
import store from "@/store";
import config from "@/plus/http/config";
export default {
  name: "project.detail",
  data() {
    return {
      footerType: 0,
      pageStatus: 0,
      host: config.host,
      tabH: 0,

      projectId:void 0,//项目intid
      projectInfo:void 0,//项目信息
      firstProject:void 0,//第一个项目信息
      parentProject:void 0,//当前项目父元素信息
    };
  },
  computed: {},
  created() {
    var id = this.$route.params.id;
    this.projectId = id.replace(/[a-zA-Z]/g, '');
    console.log(this.projectId)
    this.getProjectMsg();
  },
  activated() {},
  deactivated() {},
  mounted() {
    this.pageStatus = 2;
    this.adJustStyle();
    
  },
  methods: {
    adJustStyle() {},
    //获取项目列表
    async getProjectMsg() {
      var _this = this;
      var model = {
        id:_this.projectId
      }
      await _this.$http.project
        .getProjectDetail(model)
        .then(res => {
          if (res.data.Success) {
            var data = res.data.Content;
            _this.projectInfo = data.Project;
            console.log( _this.projectInfo)
          }
        })
        .catch(err => {
          _this.$router.replace('/error')
          console.log(err);
        });
    }
  }
};
</script>

<style lang="less" scoped>
.icon-right {
  display: block;
  width: 0.24rem;
  height: 0.24rem;
  background: url(../../assets/img/home/icon_more.png) no-repeat center;
  background-size: contain;
  margin: 0 0.1rem;
}

.p-intro {
  position: relative;
  text-align: center;
  .p-bg {
    display: block;
    width: 100%;
    height: 3.2rem;
    object-fit: cover;
    border-radius: 0.1rem;
  }
  .p-tip {
    position: absolute;
    bottom: 0.1rem;
    left: 50%;
    transform: translateX(-50%);
  }
}
.p-desc-item {
  &.border-bottom {
    border-bottom: 1px solid #f2f2f2;
  }
  .p-desc-tag {
    width: 0.64rem;
    height: 0.32rem;
    line-height: 0.32rem;
    margin-right: 0.15rem;
    border-radius: 3px;
    overflow: hidden;
    text-align: center;
    color: #fff;
    &.bk {
      background: #ff8686;
    }
    &.mx {
      background: #7ecff5;
    }
  }
  .p-desc-title {
    width: 5.8rem;
  }
  .p-desc-content {
    width: 5.4rem;
  }
  .p-lookmore {
    &:active {
      color: #999;
    }
  }
}
.icon-title {
  display: block;
  width: 0.06rem;
  height: 0.34rem;
  margin-right: 0.15rem;
  background: url(../../assets/img/common/doc.png) no-repeat center;
}
.icon-q{
  display: block;
  width: 0.24rem;
  height: 0.24rem;
  margin-right: 0.15rem;
  background: url(../../assets/img/common/icon-list.png) no-repeat center;
  background-size:contain;
}

.content-box {
  &.border-bottom{
    border-bottom:1px solid #f2f2f2;
  }
  p{
    font-size:.3rem !important;
    color:#666;
    line-height: .5rem;
  } 
}
.wrap-box{
  .border-bottom{
    border-bottom:1px solid #f2f2f2;
  }
  .question{
    .question-data{
      .data-number{
        flex:1;
        padding-right:.2rem;
      }
      .meb-recommend{
        img{
          width:.32rem;
          height:.32rem;
          margin-right:.1rem;
        }
      }
    }
  }
  .hospital{
    .hospital-head{
      width:1.28rem;
      height:1.28rem;
      border-radius: .08rem;
      object-fit:cover;
    }
    .hospital-info{
      width:5.3rem;
      height:1.28rem;
      margin-left:.3rem;
      p{
        width:100%;
      }
    }
  }
}
.cost-box{
  .cost-item{
    position:relative;
    .cost-left{
      width:.8rem;
      text-align: center;
      position:absolute;
      top:0;
      left:0;
      border-radius: 3px;
      color: #ff8686;
      border: 1px solid #ff8686;
      background:#fff;
    }
    .cost-right{
      width:6.2rem;
      margin-left:.4rem;
      padding-left:.8rem;
      border-left:1px solid #ffc2c2;
      border-bottom:1px solid #ffc2c2;
      p{
        line-height: .5rem;
      }
    }
  }

}
// 查看更多
.look-more{
  img{
    width:.4rem;
    height:.4rem;
    margin-right:.12rem;
  }
}
</style>
<style lang="css">
  .content-box p {
    font-size:.3rem;
  }
</style>


