<template>
  <m-container>
    <m-header headType="3"></m-header>
    <m-center :pageStatus="pageStatus">
      <div class="wrap">
        <img class="block" src="https://m.meb.com/Content/images/diary/diary-banner.jpg" alt="">
        <div class="bg-f5 p-b-20"></div>
      </div>
      <!-- 整形日记分类 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30" v-cloak>
        <div class="box-title flex">
          <i class="icon-title"></i>
          <p class="font-34">日记分类</p>
        </div>
        <div class="wrap-box p-t-30 diary-classify">
          <ul class="diary-list flex flex-wrap hot-tag">
            <router-link tag="li" class="diary-item hot-tag-item font-26 one-line" :to="{path:`/diary/wzx`}">微整注射</router-link>
            <router-link tag="li" class="diary-item hot-tag-item font-26 one-line" :to="{path:`/diary/ybzx`}">眼部整形</router-link>
            <router-link tag="li" class="diary-item hot-tag-item font-26 one-line" :to="{path:`/diary/bbzx`}">鼻部整形</router-link>
            <router-link tag="li" class="diary-item hot-tag-item font-26 one-line" :to="{path:`/diary/xbzx`}">胸部整形</router-link>
            <router-link tag="li" class="diary-item hot-tag-item font-26 one-line" :to="{path:`/diary/mblk`}">面部轮廓</router-link>
            <router-link tag="li" class="diary-item hot-tag-item font-26 one-line" :to="{path:`/diary/zfzx`}">脂肪整形</router-link>
            <router-link tag="li" class="diary-item hot-tag-item font-26 one-line" :to="{path:`/diary/pfmr`}">皮肤美容</router-link>
            <router-link tag="li" class="diary-item hot-tag-item font-26 one-line" :to="{path:`/diary/mbzx`}">眉部整形</router-link>
            <router-link tag="li" class="diary-item hot-tag-item font-26 one-line" :to="{path:`/diary/mfmr`}">毛发美容</router-link>
            <router-link tag="li" class="diary-item hot-tag-item font-26 one-line" :to="{path:`/diary/kczx`}">口唇整形</router-link>
            <router-link tag="li" class="diary-item hot-tag-item font-26 one-line" :to="{path:`/diary/yczx`}">牙齿整形</router-link>
            <router-link tag="li" class="diary-item hot-tag-item font-26 one-line" :to="{path:`/diary/ebzx`}">耳部整形</router-link>
          </ul>
          <router-link :to="{path:`/diary/all`}" tag="div" class="look-more flex flex-center">
            <img src="../../assets/img/common/more.png" alt />
            <span class="font-28 c-99">查看全部日记</span>
          </router-link>
        </div>
      </div>
      <div class="bg-f5 p-b-20"></div>
      <!-- 整形日记 -->
      <div class="wrap-box p-t-30 p-l-30 p-r-30 p-b-30" v-cloak v-if="diaryList">
        <div class="box-title flex">
          <i class="icon-title"></i>
          <p class="font-34">整形日记</p>
        </div>
        <div class="wrap diary-box">
          <router-link to="" tag="div" class="diary border-bottom p-t-30 p-b-30">
            <div class="user-box flex flex-between flex-top">
              <img class="user-head block" src="https://cdn-ssl.meb.com/hospital/20190617/e19c41163839475bbd84733d3e6f8d4d497-s" alt="" />
              <div class="user-msg flex flex-center flex-column flex-top">
                <p class="title font-32 c-66 two-line">秋、叶绚丽的瘦脸前后对比照效果图</p>
                <p class="m-t-15"><span class="tag font-24 c-66"><i class="normal c-meb">#</i>瘦脸</span><span class="m-l-20 tag font-24 c-66"><i class="normal c-meb">#</i>术后429天</span></p>
              </div>
            </div>
            <p class="p-t-30 font-28 c-66 two-line">
              好久不见啦，想我了没啊，最近因为要准备文艺晚会，每天都要排练到很晚，特别辛苦，因为我们是学生会的，所以每次活动都要做好带头作用，节目肯定是逃不过了，所以这次召集了很多会唱歌的同学演了一出舞台剧，很荣幸，我被选了女主角，这段时间每天都在练习，很辛苦呢，好在今天表演的很顺利，没忘词，而且我觉得我表现也挺不错的。今天心情好，给大家拍了一段小视频，看我今天是不是很漂亮呢。现在的我做什么都自信满满的，感觉自己怎样都美美的，告别了之前塌塌丑丑的鼻子，生活也变得越来越多姿多彩了，特别喜欢现在的状态，也特别庆幸自己当初做的选择，特别感谢为我做鼻子的医生，还有恢复期间帮我解答问题的医助小姐姐，没有他们我的鼻子也
            </p>
            <div class="diary-img m-t-20 flex">
              <p class="img-box before-img">
                <span class="tags font-30 c-ff">美前</span>
                <img class="block" src="https://si.meb.com/hospital/20180508/0527c81544404cb79c21246c5b8fb118937-s" alt="">
              </p>
              <p class="img-box after-img">
                <span class="tags font-30 c-ff">美后</span>
                <img class="block" src="https://si.meb.com/hospital/20180705/d2173019ecdf4cafb40ff442fc1aed0c844-s" alt="">
              </p>
              <p class="img-box more-img">
                <span class="diary-num flex flex-column flex-center">
                  <i class="normal c-meb font-50">10</i>
                  <span class="font-30 c-66 p-t-20">篇日记</span>
                </span>
                <img class="block" src="https://m.meb.com/Content/images/home/diary-more.png" alt="">
              </p>
            </div>
            <p class="m-t-20 c-99 font-26 one-line">2019-10-12</p>
          </router-link>
          <router-link to tag="div" class="look-more flex flex-center p-t-30">
            <img src="../../assets/img/common/more.png" alt />
            <span class="font-28 c-99">查看全部日记</span>
          </router-link>
        </div>
      </div>
    </m-center>
  </m-container>
</template>

<script>
//import api from '@/plus/api';
import store from "@/store";
import config from "@/plus/http/config";
export default {
  name: "diary.index",
  data() {
    return {
      footerType: 0,
      pageStatus: 0,
      host: config.host,
      
      diaryList:void 0,//最新攻略列表
    };
  },
  computed: {},
  created() {
    this.getDiary();
  },
  activated() {},
  deactivated() {},
  mounted() {
    this.pageStatus = 2;
  },
  methods: {
    //获取最新攻略
    async getDiary(){
      var _this = this;
      await _this.$http.diary.getData().then(res => {
        if(res.data.Success){
          _this.diaryList = res.data.Content;
        }
      }).catch(err=>{
        console.log(err)
      })
    }
  }
};
</script>

<style lang="less" scoped>
.icon-title {
  display: block;
  width: 0.06rem;
  height: 0.34rem;
  margin-right: 0.15rem;
  background: url(../../assets/img/common/doc.png) no-repeat center;
}

.wrap-box {
  .border-bottom {
    border-bottom: 1px solid #f2f2f2;
  }
  //日记分类
  .hot-tag {
    .hot-tag-item{
      display:block;
      width: 1.5rem;
      color:#4d4d4d;
      background:#f3f3f3;
      text-align: center;
      padding:.1rem;
      border-radius: .24rem;
      margin-right:.3rem;
      margin-bottom:.2rem;
      &:nth-child(4n){
        margin-right:0;
      }
    }
  }
  //日记
  .diary-box{
    .user-box{
      .user-head{
        width:.88rem;
        height:.88rem;
        border-radius: 50%;
        margin-right:.3rem;
      }
      .user-msg{
        width:calc(100% - .88rem - .3rem);
        min-height: .88rem;
        .tag{
          padding:.05rem .1rem;
          background:#f3f3f3;
          border-radius: .08rem;
        }
      }
    }
    .diary-img{
      .img-box{
        width:calc( (100% - .4rem)/3);
        margin-right:.2rem;
        border-radius: .1rem;
        overflow: hidden;
        position:relative;
        top:0;
        left:0;
        &:nth-child(3n){
          margin-right:0;
        }
        .tags{
          position:absolute;
          left:0;
          bottom:0;
          padding: .02rem .1rem;
          border-top-right-radius: .16rem;
          border-bottom-left-radius: .1rem;
        }
      }
      .before-img{
        .tags{
          background: #0c0908;
          opacity: .9;
        }
      }
      .after-img{
        .tags{
          background: #ff8686;
          opacity: .9;
        }
      }
      .more-img{
        .diary-num{
          position:absolute;
          top:0;
          left:0;
          right:0;
          bottom:0;
        }
      }
    }
  }
  // 查看更多
  .look-more {
    img {
      width: 0.4rem;
      height: 0.4rem;
      margin-right: 0.12rem;
    }
  }
}
</style>


