<template>
  <m-container>
    <m-header headType="3" @opensearch="opensearch"></m-header>
    <m-footer :footerType="footerType"></m-footer>
    <m-center :pageStatus="pageStatus">
      <div class="m-content animated fadeIn" v-cloak>
        <!-- 轮播 -->
        <div v-if="homeData && homeData.TopBanner"> 
          <div class="m-banner" v-cloak>
            <swiper class="m-swiper" :options="swiperOption" v-if="homeData.TopBanner && homeData.TopBanner.length>0">
              <swiper-slide class="swiper-slide" v-for="(item,index) in homeData.TopBanner" :key="index">
                <a :href="item.Url" replace>
                  <img v-lazy="item.PictureUrl" :alt="item.Name" />
                </a>
              </swiper-slide>
              <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
          </div>
        </div>
        <!-- 类目 -->
        <nav class="m-nav" v-cloak>
          <div class="wrap flex flex-wrap p-l-30 p-r-30 p-b-20">
            <router-link
              :to="nav.url"
              class="nav-item flex flex-column flex-center"
              v-for="(nav,i) in navlist"
              :key="i"
            >
              <img v-lazy="nav.img" :alt="nav.name" />
              <p>{{nav.name}}</p>
            </router-link>
          </div>
        </nav>
        <!-- 广告位1 -->
        <section class="m-ad" v-cloak>
          <div class="wrap m-r-30 m-l-30 p-t-20" v-if="adOne && adOne.Url">
            <a :href="adOne.Url" class="ad-item">
              <img v-lazy="adOne.PictureUrl" :alt="adOne.Name" />
            </a>
          </div>
        </section>
        <!-- 微整特价 -->
        <div class="wrap" v-cloak v-if="homeData && homeData.Products">
          <div class="title-head flex p-l-30 p-r-30 p-t-20 p-b-20">
            <div class="title-left ls-02 font-38 one-line">
              <img src="../../assets/img/home/zxtj.png" alt="">
            </div>
            <router-link class="title-right more flex one-line" to="">
              <span class="font-28 one-line">查看更多</span>
            </router-link>
          </div>
          <swiper class="scroll-view p-l-30 p-r-30 p-t-10 p-b-30" :options="scllorView">
            <swiper-slide class="swiper-slide" v-for="(products,index) in homeData.Products" :key="index">
              <router-link class="p-box" to="">
                <img class="p-img" v-lazy="products.Image" alt="">
                <div class="p-msg flex flex-column flex-top p-t-20 p-b-20">
                  <p class="p-name bold font-30 one-line">{{products.Name}}</p>
                  <p class="p-h-name font-26 m-t-10 one-line">{{products.Hospital.Name}}</p>
                  <p class="p-price font-24 m-t-10 one-line c-aa"><span class="font-28 bold c-meb p-r-20">{{products.Price}}</span><span class="through">{{products.ShowPrice}}</span></p>
                </div>
              </router-link>
            </swiper-slide>
          </swiper>
        </div>
        <!-- 广告位2 -->
        <section class="m-ad" v-cloak>
          <div class="wrap m-r-30 m-l-30 p-t-20 p-b-20" v-if="adTwo && adTwo.Url">
            <a :href="adTwo.Url" class="ad-item">
              <img :src="adTwo.PictureUrl" :alt="adTwo.Name" />
            </a>
          </div>
        </section>
        <!-- 广告咨询 -->
        <section class="m-custom" v-cloak>
          <div class="wrap flex flex-between bg-f5 p-r-30 p-l-30 p-t-30 p-b-30">
            <div class="m-custom-left">
              <a href=""><img src="../../assets/img/home/banner3.png" alt=""></a>
            </div>
            <div class="m-custom-right flex flex-column flex-between">
              <a href=""><img src="../../assets/img/home/banner1.png" alt=""></a>
              <a href=""><img src="../../assets/img/home/banner2.png" alt=""></a>
            </div>
          </div>
        </section>
        <!-- 本地医院 -->
        <div class="m-hospital" v-cloak v-if="homeData && homeData.Hospitals">
          <div class="title-head flex p-l-30 p-r-30 p-t-30 p-b-20">
            <div class="title-left font-38 one-line">
              <p class="font-34 ls-02 c-hospital bold">本地医院</p>
              <p class="font-22 m-t-10 c-99">全国10万家优质医院</p>
            </div>
            <router-link class="title-right more flex one-line" :to="{path:`/hospital/all`}">
              <span class="font-28 one-line">更多医院</span>
            </router-link>
          </div>
          <swiper class="scroll-view p-l-30 p-r-30 p-t-10 p-b-30" 
            :options="scllorDemo"
            v-if="homeData.Hospitals && homeData.Hospitals.length>0">
            <swiper-slide class="swiper-slide" v-for="(hospital,index) in homeData.Hospitals" :key="index">
              <router-link class="d-h-box text-c flex flex-column p-b-30 p-t-30 p-l-30 p-r-30" to="">
                <img class="d-h-img" v-lazy="hospital.Image" alt="">
                <div class="d-h-msg flex flex-column p-t-20 p-b-20">
                  <p class="d-h-name font-28 one-line">{{hospital.Name}}</p>
                  <p class="d-h-case font-24 m-t-10 one-line c-aa">
                    <span class="font-24 c-meb p-r-20">{{hospital.CaseNumber}}</span>例案例
                  </p>
                </div>
              </router-link>
            </swiper-slide>
          </swiper>
          <div class="bg-f5 p-b-20"></div>
        </div>
        <!-- 本地医生 -->
        <div class="m-doctor" v-cloak v-if="homeData && homeData.Doctors">
          <div class="title-head flex p-l-30 p-r-30 p-t-30 p-b-20">
            <div class="title-left font-38 one-line">
              <p class="font-34 ls-02 c-doctor bold">本地医生</p>
              <p class="font-22 m-t-10 c-99">全国30万名优质医生</p>
            </div>
            <router-link class="title-right more flex one-line" :to="{path:`/hospital/all`}">
              <span class="font-28 one-line">更多医生</span>
            </router-link>
          </div>
          <swiper class="scroll-view p-l-30 p-r-30 p-t-10 p-b-30" 
            :options="scllorDemo"
            v-if="homeData.Doctors && homeData.Doctors.length>0">
            <swiper-slide class="swiper-slide" v-for="(doctor,index) in homeData.Doctors" :key="index">
              <router-link class="d-h-box text-c flex flex-column p-b-30 p-t-30 p-l-30 p-r-30" to="">
                <img class="d-h-img" v-lazy="doctor.Image" alt="">
                <div class="d-h-msg flex flex-column p-t-20 p-b-20">
                  <p class="d-h-name font-28 one-line">{{doctor.Name}}</p>
                  <p class="d-h-case font-24 m-t-10 one-line c-aa">
                    <span class="font-24 c-meb p-r-20">{{doctor.CaseNumber}}</span>例案例
                  </p>
                </div>
              </router-link>
            </swiper-slide>
          </swiper>
          <div class="bg-f5 p-b-20"></div>
        </div>
        <!-- 整形日记 -->
        <div class="m-diary" v-cloak v-if="homeData && homeData.Diaries">
          <div class="title-head flex p-l-30 p-r-30 p-t-30 p-b-20">
            <div class="title-left font-38 one-line">
              <p class="font-34 ls-02 c-diary bold">整形日记</p>
              <p class="font-22 m-t-10 c-99">看看大家有多美</p>
            </div>
          </div>
          <swiper class="scroll-view p-l-30 p-r-30 p-t-10 p-b-30" 
            :options="scllorDemo"
            v-if="homeData.Diaries && homeData.Diaries.length>0">
            <swiper-slide class="swiper-slide" v-for="(diary,index) in homeData.Diaries" :key="index">
              <router-link class="diary-box text-c flex flex-column relative" to="">
                <img class="diary-mask block" v-lazy="'//cdn-ssl.meb.com/hot_'+(diary.SpellName == 'ztzftc'?'ztzf':diary.SpellName)+'.png'" alt="">
                <div class="diary-msg absolute">
                  <div class="diary-info flex flex-column flex-center">
                    <p class="diary-name c-ff bold font-30 p-b-10 border-b">{{diary.Name}}</p>
                    <p class="diary-case p-t-10 c-ff font-24"><span class="c-ffea61 p-r-10">{{diary.DiaryCount}}</span>篇</p>
                  </div>
                </div>
              </router-link>
            </swiper-slide>
            <swiper-slide class="swiper-slide" v-if="homeData.Diaries && homeData.Diaries.length>0">
              <router-link class="diary-box text-c flex flex-column relative" to="">
                <img class="diary-mask block" src="//cdn-ssl.meb.com/hot_diary-mores.png" alt="">
                <div class="diary-msg absolute">
                  <div class="diary-info flex flex-column flex-center">
                    <p class="diary-name c-ff bold font-30 p-b-10 border-b">更多</p>
                  </div>
                </div>
              </router-link>
            </swiper-slide>
          </swiper>
          <div class="bg-f5 p-b-20"></div>
        </div>
        <!-- 整形必读 -->
        <div class="m-zx" v-cloak>
          <div class="title-head flex p-l-30 p-r-30 p-t-30 p-b-20">
            <div class="title-left font-38 one-line">
              <p class="font-34 ls-02 c-meb bold">整形必读</p>
              <p class="font-22 m-t-10 c-99">不可错过的变美贴士</p>
            </div>
          </div>
          <swiper :options="swiperOptionTop" class="scroll-view swiper-nav p-t-10 p-b-10" ref="swiperTop">
            <swiper-slide class="swiper-slide">
              <p class="nav-tag text-c font-32" :class="{'active':navIndex == 0}" @click="slidTo(0)">攻略</p>
            </swiper-slide>
            <swiper-slide class="swiper-slide">
              <p class="nav-tag text-c font-32" :class="{'active':navIndex == 1}" @click="slidTo(1)">知识</p>
            </swiper-slide>
            <swiper-slide class="swiper-slide">
              <p class="nav-tag text-c font-32" :class="{'active':navIndex == 2}" @click="slidTo(2)">问答</p>
            </swiper-slide>
            <swiper-slide class="swiper-slide">
              <p class="nav-tag text-c font-32" :class="{'active':navIndex == 3}" @click="slidTo(3)">视频</p>
            </swiper-slide>
            <swiper-slide class="swiper-slide">
              <p class="nav-tag text-c font-32" :class="{'active':navIndex == 4}" @click="slidTo(4)">商城</p>
            </swiper-slide>
          </swiper>
          <!-- swiper2 Thumbs -->
          <swiper :options="swiperOptionThumbs" class="scroll-view swiper-tab p-l-30 p-r-30 p-t-10 p-b-30" ref="swiperThumbs">
            <!-- 攻略 -->
            <swiper-slide class="swiper-slide">
              <div v-if="cmsData">
                <router-link 
                  v-for="(newsItem,newsIndex) in cmsData.News" :key="newsIndex"
                  class="flex relative bottom-border p-t-20 p-b-20" :to="{path:`/news/${newsItem.IntId}.html`}">
                  <div class="gn-box flex flex-between">
                    <p class="title font-30 two-line">{{newsItem.Title}}</p>
                    <img class="show-img" :src="newsItem.ImgUrls[0]" alt="">
                  </div>
                </router-link>
              </div>
              <router-link class="flex relative p-t-40 p-b-40" to="" v-if="cmsData && cmsData.News && cmsData.News.length>0">
                <div class="gn-box flex flex-center font-24 c-b2">查看更多攻略</div>
              </router-link>
            </swiper-slide>
            <!-- 知识 -->
            <swiper-slide class="swiper-slide">
              <div v-if="cmsData">
                <router-link 
                  v-for="(zsItem,zsIndex) in cmsData.Zs" :key="zsIndex"
                  class="flex relative bottom-border p-t-20 p-b-20" :to="{path:`/zs/${zsItem.IntId}.html`}">
                  <div class="zs-box gn-box flex flex-between">
                    <p class="title font-30 two-line">{{zsItem.Title}}</p>
                    <img class="show-img" :src="zsItem.ImgUrls[0]" alt="">
                  </div>
                </router-link>
              </div>
              <router-link class="flex relative p-t-40 p-b-40" to="" v-if="cmsData && cmsData.Zs && cmsData.Zs.length>0">
                <div class="gn-box flex flex-center font-24 c-b2">查看更多知识</div>
              </router-link>
            </swiper-slide>
            <!-- 问答 -->
            <swiper-slide class="swiper-slide">
              <div v-if="cmsData">
                <router-link 
                  v-for="(qItem,qIndex) in cmsData.Questions" :key="qIndex"
                  class="flex relative bottom-border p-t-20 p-b-20" 
                  :to="{path:`/question/${qItem.IntId}.html`}">
                  <div class="flex flex-column flex-between flex-top question-box">
                    <p class="title font-30 two-line">{{qItem.Title}}</p>
                    <p class="question-num p-t-10 c-b2"><span class="c-meb font-24 p-r-10">{{qItem.AnswerCount}}</span>条回答</p>
                  </div>
                </router-link>
              </div>
              <router-link class="flex relative p-t-40 p-b-40" :to="{path:`/question/all`}" v-if="cmsData && cmsData.Questions && cmsData.Questions.length>0">
                <div class="gn-box flex flex-center font-24 c-b2">查看更多问答</div>
              </router-link>
            </swiper-slide>
            <!-- 视频 -->
            <swiper-slide class="swiper-slide">
              <div v-if="cmsData" class="video-box flex flex-between flex-wrap relative bottom-border p-t-20 p-b-20">
                <router-link 
                  v-for="(vItem,vIndex) in cmsData.Videos" :key="vIndex"
                  class="video-item flex flex-column flex-between relative m-b-30" to="">
                  <div class="v-video m-b-20 relative">
                    <img :src="vItem.ImageUrl" alt="" >
                    <em class="splistbtn big-df"><i></i></em>
                  </div>
                  <p class="title font-30 one-line">{{vItem.Title}}</p>
                </router-link>
              </div>
              <router-link class="flex relative p-t-40 p-b-40" to="" v-if="cmsData && cmsData.Videos && cmsData.Videos.length>0">
                <div class="gn-box flex flex-center font-24 c-b2">查看更多视频</div>
              </router-link>
            </swiper-slide>
            <!-- 商城 -->
            <swiper-slide class="swiper-slide">
              <div class="product-box" v-if="homeData && homeData.Products">
                <div class="block relative bottom-border p-t-20 p-b-20" v-for="(product,index) in homeData.Products" :key="index">
                  <router-link class="shop-box flex flex-between flex-top p-b-30" to="">
                    <img class="shop-img" v-lazy="product.Image" alt="">
                    <div class="shop-product flex flex-column flex-top">
                      <p class="shop-name one-line font-30 p-b-20">{{product.Name}}</p>
                      <p class="shop-price p-b-20 one-line"><span class="c-meb font-30 p-r-20 bold">{{product.Price}}</span><span class="font-24 c-aa through">{{product.ShowPrice}}</span></p>
                      <p class="shop-hospital font-24 m-b-20 one-line c-80">{{product.Hospital.Name}}</p>
                      <p class="shop-sale font-28 c-99 one-line flex"><i class="icon-cart m-r-10"></i>已售出：<span>{{product.SaleNumber}}</span>份</p>
                    </div>
                  </router-link>
                  <div class="shop-tag bg-f6 p-l-20 m-b-10 flex" v-if="product.Supportabilities && product.Supportabilities.length>0">
                    <div v-for="(pro,i) in product.Supportabilities" :key="i">
                      <p class="font-24 flex m-r-30" v-if="pro.Icon == '分'"><span class="shop-tag-item fen">分</span>支持分期</p>
                      <p class="font-24 flex m-r-30" v-if="pro.Icon == '券'"><span class="shop-tag-item quan">券</span>领券</p>
                      <p class="font-24 flex" v-if="pro.Icon == '积'"><span class="shop-tag-item ji">积</span>积分抵扣</p>
                    </div>
                  </div>
                </div>
                <router-link class="flex relative p-t-40 p-b-40" to="" v-if="homeData.Products.length>0">
                  <div class="gn-box flex flex-center font-24 c-b2">更多美容项目</div>
                </router-link>
              </div>
            </swiper-slide>
          </swiper>
        </div>
      </div>

    </m-center>
  </m-container>
</template>

<script>
import store from "@/store";
import config from "@/plus/http/config";
export default {
  name: "index",
  data() {
    return {
      footerType: 0,
      pageStatus: 0,
      host: config.host,

      homeData:void 0,//首页数据

      //轮播数据
      mBannerData: [
        {
          Name: "开学季活动",
          Url: "https://m.meb.com/citysummeractive/schoolopens/",
          PictureUrl: "https://cdn-ssl.meb.com/2019/09/04/1540446244-banner"
        },
        {
          Name: "9.9活动",
          Url: "https://m.meb.com/citysummeractive/actninelist/",
          PictureUrl: "https://cdn-ssl.meb.com/2018/08/31/0950276094-banner"
        }
      ],
      //轮播配置
      swiperOption: {
        slidesPerView: 1,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        spaceBetween: 15,
        loop: false
      },
      //nav数据
      navlist: [
        { name: "免费咨询", url: "", img: "//cdn-ssl.meb.com/mfzx.png" },
        { name: "项目百科", url: "/project", img: "//cdn-ssl.meb.com/xmbk.png" },
        { name: "整形攻略", url: "/news", img: "//cdn-ssl.meb.com/zxgl.png" },
        { name: "整形问答", url: "/question/all", img: "//cdn-ssl.meb.com/zxwd.png" },
        { name: "整形日记", url: "", img: "//cdn-ssl.meb.com/mrrj.png" },
        { name: "整形医院", url: "hospital/all", img: "//cdn-ssl.meb.com/zmyy.png" },
        { name: "整形医生", url: "", img: "//cdn-ssl.meb.com/zmys.png" },
        { name: "美呗美淘", url: "", img: "//cdn-ssl.meb.com/mbmt.png" },
        { name: "美呗视频", url: "", img: "//cdn-ssl.meb.com/mbsp.png" },
        { name: "VIP服务", url: "", img: "//cdn-ssl.meb.com/vip.png" }
      ],
      //广告位
      adOne: void 0,
      adTwo: void 0,
      //微整
      scllorView: {
        slidesPerView: 2.1,
        spaceBetween: 15,
        // freeMode: true
      },
      //医生、医院、日记
      scllorDemo: {
        slidesPerView: 2,
        spaceBetween: 20,
        // freeMode: true
      },
      //日记banner
      diarySwiper:[
        { spellName:'xz', url:'', Name:'吸脂', DiaryCount:'157', Image:'//cdn-ssl.meb.com/hot_xz.png' },
        { spellName:'syp', url:'', Name:'双眼皮', DiaryCount:'266', Image:'//cdn-ssl.meb.com/hot_syp.png' },
        { spellName:'bns', url:'', Name:'玻尿酸', DiaryCount:'22', Image:'//cdn-ssl.meb.com/hot_bns.png' },
        { spellName:'lx', url:'', Name:'隆胸', DiaryCount:'157', Image:'//cdn-ssl.meb.com/hot_lx.png' },
        { spellName:'ztzftc', url:'', Name:'自体脂肪填充', DiaryCount:'157', Image:'//cdn-ssl.meb.com/hot_ztzf.png' },
        { spellName:'lb', url:'', Name:'隆鼻', DiaryCount:'291', Image:'//cdn-ssl.meb.com/hot_lb.png'},
        { spellName:'gd', url:'', Name:'更多', DiaryCount:'157', Image:'//cdn-ssl.meb.com/hot_diary-mores.png' },
      ],
      //整形必读
      navIndex:0,//初始化index
      swiperOptionTop: {
        slidesPerView: 5,
        freeMode: false,
        initialSlide: 0
      },
      swiperOptionThumbs: {
        spaceBetween: 20,
        autoHeight: true,
        centeredSlides: true,
        slidesPerView: 1,
        initialSlide: 0,
        on:{
          slideChange: ()=>{
            let swiperThumbs = this.$refs.swiperThumbs.swiper;
            this.navIndex = swiperThumbs.activeIndex;
          }
        }
      },
      //问答、知识、视频、攻略
      cmsData:void 0,
    };
  },
  computed: {
    swiperTop(){
      return this.$refs.swiperTop.swiper;
    },
    swiperThumbs(){
      return this.$refs.swiperThumbs.swiper;
    },
    //...mapState(["onLoad"]),
  },
  filters: {},
  activated() {},
  deactivated(){},
  mounted() {
    this.getHomeData();
    this.getAdData();
    //this.getWzData();
    
    //swiper-双向控制
    this.swiper();

    this.getCmsData();
  },
  methods: {
    // load(){
    //   this.$store.dispatch('loadchange', {
    //     onLoad: true
    //   })
    // },
    opensearch(keyword){
      console.log('点击搜索')
    },
    //swiper-双向控制
    slidTo(index){
      event.preventDefault();
      this.navIndex = index;
      this.swiperThumbs.slideTo(this.navIndex, 200, false);
    },

    swiper(){
      this.$nextTick(() => {
        // this.swiperTop.controller.control = this.swiperThumbs
        this.swiperThumbs.controller.control = this.swiperTop
      })
      this.swiperThumbs.on('transitionEnd',() => {
        //this.swiperThumbs.slideTo(this.navIndex, 100, false);
        this.swiperThumbs.activeIndex = this.navIndex;
      })
    },
    //获取首页数据
    async getHomeData() {
      var _this = this;
      var model = {
        cityId:'a2c11ad0-3f24-4543-9d22-140fd7b2bc00'//成都
      }
      await _this.$http.home.getData(model).then( res => {
        if(res.data.Success){
          _this.pageStatus = 2;
          _this.homeData = res.data.Content;
        }
      }).catch(err=>{
        console.log(err)
      })
    },
    //获取广告数据
    async getAdData() {
      var _this = this;
      await _this.$http.home
        .getAdData()
        .then(res => {
          if (res.data.Success) {
            var data = res.data.Content;
            _this.adOne = data.ProjectAd;
            _this.adTwo = data.CityAd;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },

    //获取微整商品列表
    async getWzData() {
      var _this = this;
      var model = {
        areaid:'a2c11ad0-3f24-4543-9d22-140fd7b2bc00',
        page:1,
        size:5
      }
      await _this.$http.home
        .getWzData(model)
        .then(res => {
          if (res.data.Success) {
            var data = res.data.Content;
            console.log(data)
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //获取cms数据
    getCmsData(){
      var _this = this;
      _this.$http.home
        .getCmsData()
        .then(res => {
          if (res.data.Success) {
            var data = res.data.Content;
            _this.cmsData = data;
          }
        })
    }
  }
};
</script>

<style lang="less" scoped>
.m-banner {
  width: 6.9rem;
  height: 3.3rem;
  position: relative;
  margin: 0.3rem auto;
  border-radius: 0.12rem;
  overflow: hidden;
  box-shadow: 0 2px 30px 2px rgba(99, 99, 99, 0.2);
  -webkit-box-shadow: 0 2px 30px 2px rgba(99, 99, 99, 0.2);
  -moz-box-shadow: 0 2px 30px 2px rgba(99, 99, 99, 0.2);
  .m-swiper {
    width: 100%;
    height: 100%;
    .swiper-slide {
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 0.12rem;
      }
    }
    & > .swiper-pagination-bullets {
      bottom: 5px;
      left: 0;
      width: 100%;
      .swiper-pagination-bullet {
        width: 10px;
        height: 10px;
        box-shadow: 0px 0px 1px 2px #fff inset;
      }
    }
  }
}
.m-nav {
  .nav-item {
    text-align: center;
    margin-right: 0.36rem;
    &:active {
      p {
        color: #999;
      }
    }
    &:nth-child(5n) {
      margin-right: 0;
    }
    &:nth-child(n + 6) {
      margin-top: 0.2rem;
    }
    img {
      width: 1.09rem;
      height: 1.09rem;
    }
    p {
      color: #666;
    }
  }
}
.m-ad {
  width: 100%;
  position: relative;
  .ad-item {
    img {
      width: 100%;
    }
  }
}
.scroll-view{
  touch-action: none;
  // 微整列表
  .p-box{
    width:100%;
    display: block;
    overflow: hidden;
    border-radius: .1rem;
    .p-img{
      display:block;
      width:100%;
      height:2.5rem;
      object-fit: cover;
    }
    .p-msg{
      width: 100%;
      background:#F6F6F6;
      padding: .2rem .1rem;
      p{
        width:100%;
      }
      .p-name{
        line-height: .4rem;
        height: .4rem;
        color: #2A2A2A;
      }
      .p-h-name{
        color: #666666;
      }
      .p-price{
        height: .5rem;
        line-height: .5rem;
        overflow: hidden;
      }
    }
  }
  // 医院
  .d-h-box{
    width:100%;
    display: block;
    overflow: hidden;
    border-radius: .1rem;
    -webkit-box-shadow: 0 1px 30px 0 rgba(232, 232, 232, 0.5), 0 1px 28px 0 rgba(232, 232, 232, 0.2);
    box-shadow: 0 1px 30px 0 rgba(232, 232, 232, 0.5), 0 1px 28px 0 rgba(232, 232, 232, 0.2);
    .d-h-img{
      width: 1.28rem;
      height: 1.28rem;
      border-radius: 50%;
      overflow: hidden;
      margin: .4rem auto 0;
    }
    .d-h-msg{
      text-align:center;
      .d-h-name{
        width:100%;
      }
      .d-h-case{
        width:100%;
      }
    }
  }
  //日记
  .diary-box{
    width:100%;
    height:4rem;
    display: block;
    overflow: hidden;
    border-radius: .15rem;
    .diary-mask{
      display:block;
      width:100%;
      height:100%;
      object-fit: cover;
    }
    .diary-msg{
      width:100%;
      height:100%;
      top:0;
      left: 0;
      bottom:0;
      right:0;
      background-color: rgba(0,0,0,.3);
      .diary-info{
        width:100%;
        height:100%;
        .border-b{
          border-bottom:2px solid #fff;
        }
      }
    }
  }
  //整形必读
  .swiper-nav{
    .swiper-slide{
      width:1rem;
    }
  }
}
.m-custom{
  .m-custom-left{
    width:2.9rem;
    height:2.9rem;
    a{
      display:block;
      >img{
        display: block;
        width:100%;
        height:100%;
      }
    }
  }
  .m-custom-right{
    width:3.7rem;
    height:2.9rem;
    margin-left:.3rem;
    padding:.06rem 0;
    a{
      display:block;
      >img{
        display: block;
        width:100%;
      }
    }
  }
}
.swiper-nav{
  touch-action: none;
  .swiper-slide{
    .nav-tag{
      color:#666;
      width:100%;
      height:.6rem;
      padding:0 .1rem;
      overflow: hidden;
      position:relative;
      &.active{
        color: #333;
        font-weight: 600;
        &::after{
          content:"";
          display:block;
          position:absolute;
          bottom:0;
          left:50%;
          transform: translateX(-50%);
          width:50%;
          height:.08rem;
          border-radius: 1rem;
          background:#ff6666;
        }
      }
    }
    
  }
}
.swiper-tab{
  .bottom-border{
    &:after{
      content:"";
      width:100%;
      height:1px;
      background:#eee;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
  //攻略
  .gn-box{
    width:100%;
    .title{
      width:4rem;
      line-height: .48rem;
      color:#4d4d4d;
    }
    .show-img{
      display:block;
      width: 2rem;
      height: 1.28rem;
      border-radius: 8px;
      overflow: hidden;
      object-fit: cover;
    }
  }
  //问答
  .question-box{
    width:100%;
    .title{
      line-height: .48rem;
    }
  }
  //视频
  .video-box{
    width:100%;
    .video-item{
      width:3.3rem;
      .v-video{
        width:100%;
        height:2.2rem;
        border-radius: .1rem;
        overflow: hidden;
        box-shadow: 0 4px 10px 2px rgba(99, 99, 99, 0.2);
        -webkit-box-shadow: 0 4px 10px 2px rgba(99, 99, 99, 0.2);
        -moz-box-shadow: 0 4px 10px 2px rgba(99, 99, 99, 0.2);
        img{
          width:100%;
          height:100%;
          object-fit: cover;
          -webkit-filter: grayscale(50%);
          filter: grayscale(50%);
        }
        .splistbtn{
          display: block;
          position: absolute;
          top: 50%;
          left: 50%;
          z-index: 10;
          width: .48rem;
          height: .48rem;
          margin: -.24rem 0 0 -.24rem;
          border-radius: 50%;
          background-color: rgba(225,102,102,.7);
          overflow: hidden;
          i{
            position: relative;
            display: block;
            width: .48rem;
            height: .48rem;
            &:after{
              position: absolute;
              top: .1rem;
              left: .17rem;
              content: " ";
              width: 0;
              height: 0;
              border-top: .14rem solid transparent;
              border-bottom: .14rem solid transparent;
              border-left: .18rem solid #fff;
            }
          }
        }
      }
      .title{
        width:100%;
      }
    }
  }
  //商城
  .shop-box{
    width:100%;
    .shop-img{
      width:2.1rem;
      height:2.1rem;
      object-fit: cover;
      border-radius: .1rem;
    }
    .shop-product{
      width:4.5rem;
      p{
        width:100%;
      }
      .shop-hospital{
        width:auto;
        max-width:100%;
        background:#f8dbda;
        padding:.05rem .18rem;
        border-radius: .25rem;
      }
      .shop-sale{
        .icon-cart{
          display:inline-block;
          width:.28rem;
          height:.28rem;
          background:url(../../assets/img/common/car.png) no-repeat center;
          background-size:contain;
        }
      }
    }
  }
  .shop-tag{
    width:100%;
    height:.6rem;
    .shop-tag-item{
      width: .32rem;
      height: .32rem;
      line-height: .32rem;
      margin-right:.1rem;
      display:inline-block;
      overflow: hidden;
      &.fen{
        border: 1px solid #ffb74d;
        color: #ffb74d;
        border-radius: 50%;
      }
      &.quan{
        border: 1px solid #ff5c5c;
        color: #ff5c5c;
        border-radius: 50%;
      }
      &.ji{
        border: 1px solid #6bacfc;
        color: #6bacfc;
        border-radius: 50%;
      }
    }
  }
}
</style>
<style lang="css" scoped>
/* 轮播分页按钮样式修改 */
.m-swiper >>> .swiper-pagination-bullets {
  bottom: 5px;
  left: 0;
  width: 100%;
}
.m-swiper >>> .swiper-pagination-bullet {
  margin: 0 3px;
  width: 10px;
  height: 10px;
  box-shadow: 0px 0px 1px 2px #fff inset;
}
.m-swiper >>> .swiper-pagination-bullet-active {
  background: #fff;
  box-shadow: 0px 0px 1px 3px rgba(0, 0, 0, 0.6) inset;
}
</style>


