<template>
  <page title="Picker 组件" desc="picker 组件是移动端常见的选择器组件，支持单列和多列；可以动态改变 picker 某列的数据，实现级联的效果。">
    <div slot="content">
      <picker-render></picker-render>
    </div>
  </page>
</template>

<script type="text/ecmascript-6">
  import Page from '@/components/picker/page.vue'
  import PickerRender from '@/components/picker/picker-render.vue'

  export default {
        name:'picker',
        components: {
            Page,
            PickerRender
        },
        data() {
            return{
                
            }
        }
  }
</script>



