<template>
  <m-container>
    <m-header headType="3"></m-header>
    <m-center :pageStatus="pageStatus">
      <!-- 导航 -->
      <nav class="p-nav p-t-20 p-b-20 p-l-30 p-r-30 c-7f flex border-bottom border-box" v-cloak>
        <router-link to="/" class="font-32">美呗</router-link>
        <i class="icon-right"></i>
        <router-link to="/news" class="font-32">整形日记</router-link>
        <template v-if="firstData">
          <template v-if="firstData && secondData">
            <i class="icon-right"></i>
            <router-link class="font-32" :to="{path:`/diary/${firstData.SpellName}`}">{{firstData.Name}}</router-link>
            <i class="icon-right"></i>
            <span class="font-32">{{secondData.Name}}</span>
          </template>
          <template v-else>
            <i class="icon-right"></i>
            <span class="font-32">{{firstData.Name}}</span>
          </template>
        </template>
      </nav>
      <div class="filtrate-box" v-cloak>
        <section class="nav-filtrate flex flex-between p-t-20 p-b-20">
          <nav class="filtrate-item flex flex-center" :class="{'menu-active':selected == 0}" @click="tapMenu(0)">
            <span class="nav-name font-28" v-if="firstData">{{firstData.Name}}</span>
            <span class="nav-name font-28" v-else>部位</span>
            <!-- <i :class="selected == 0 ? 'icon-arrow-up' : 'icon-arrow-down'"></i> -->
            <!-- <i :class="selected == 0 ? 'icon-arrow' : 'icon-arrow'"></i> -->
            <i class="icon-arrow"></i>
          </nav>
          <nav class="filtrate-item flex flex-center" :class="{'menu-active':selected == 1}" @click="tapMenu(1)">
            <span class="nav-name font-28" v-if="secondData">{{secondData.Name}}</span>
            <span class="nav-name font-28" v-else>项目名称</span>
            <!-- <i :class="selected == 1 ? 'icon-arrow-up' : 'icon-arrow-down'"></i> -->
            <i class="icon-arrow"></i>
          </nav>
          <nav class="filtrate-item flex flex-center" :class="{'menu-active':selected == 2}" @click="tapMenu(2)">
            <span class="nav-name font-28" v-if="filter.parm == 0">全部日记</span>
            <span class="nav-name font-28" v-else-if="filter.parm == 1">最新日记</span>
            <span class="nav-name font-28" v-else>最热日记</span>
            <!-- <i :class="selected == 2 ? 'icon-arrow-up' : 'icon-arrow-down'"></i> -->
            <i class="icon-arrow"></i>
          </nav>
        </section>
        <div class="filtrate-container" :style="{height:tabH + 'px'}" v-show="showMenu">
          <div class="filtrate-mask" @click="closeModal"></div>

          <ul class="filtrate-data" v-if="selected == 0">
            <router-link to="" tag="li" class="filters font-30 c-66"
              :class="{'choose' : firstData.SpellName == 'all'}"
              @click.native="chooseProject('all')">全部项目</router-link>
            <router-link to="" tag="li" class="filters font-30 c-66"
              v-for="(item,index) in filterData" :key="index"
              :class="{'choose' : firstData.SpellName == item.SpellName}"
              @click.native="chooseProject(item.SpellName)">{{item.Name}}</router-link>
          </ul>

          <ul class="filtrate-data" v-if="selected == 1">
            <div v-if="firstData">
              <div v-for="(second,i) in filterData" :key="i">
                <template v-show="second.SpellName == firstData.SpellName">
                  <router-link to="" tag="li" class="filters font-30 c-66"
                    v-show="second.SpellName == firstData.SpellName"
                    v-for="items in second.Childs" :key="items.Id"
                    :class="{'choose' : filter.sname == items.SpellName}"
                    @click.native="chooseProject(items.SpellName)"
                    >{{items.Name}}</router-link>
                </template>
              </div>
            </div>
            <router-link v-if="filter.sname == 'all'" to="" tag="li" class="filters font-30 c-66 choose" replace>全部</router-link>
          </ul>

          <ul class="filtrate-data" v-if="selected == 2">
            <router-link to="" tag="li" class="filters font-30 c-66" :class="{'choose' : filter.parm == 0 }" @click.native="tapClassical(0)">全部日记</router-link>
            <router-link to="" tag="li" class="filters font-30 c-66" :class="{'choose' : filter.parm == 1 }" @click.native="tapClassical(1)">最新日记</router-link>
            <router-link to="" tag="li" class="filters font-30 c-66" :class="{'choose' : filter.parm == 2 }" @click.native="tapClassical(2)">最热日记</router-link>
          </ul>
        </div>
      </div>
      <div class="result-box" v-if="diaryList" v-cloak>
        <div class="bg-f5 p-b-20"></div>
        <ul class="result-list p-l-30 p-r-30">
          <router-link tag="li" class="result-item border-bottom p-t-30 p-b-30"
            v-for="(item,i) in diaryList" :key="i"
            :to="{path:`/news/${item.StateShowID}.html`}">
            <div class="user-box flex flex-between flex-top">
              <img class="user-head block" src="https://cdn-ssl.meb.com/hospital/20190617/e19c41163839475bbd84733d3e6f8d4d497-s" alt="" />
              <div class="user-msg flex flex-center flex-column flex-top">
                <p class="title font-32 c-66 two-line">秋、叶绚丽的瘦脸前后对比照效果图</p>
                <p class="m-t-15"><span class="tag font-24 c-66"><i class="normal c-meb">#</i>瘦脸</span><span class="m-l-20 tag font-24 c-66"><i class="normal c-meb">#</i>术后429天</span></p>
              </div>
            </div>
            <p class="p-t-30 font-28 c-66 two-line">
              好久不见啦，想我了没啊，最近因为要准备文艺晚会，每天都要排练到很晚，特别辛苦，因为我们是学生会的，所以每次活动都要做好带头作用，节目肯定是逃不过了，所以这次召集了很多会唱歌的同学演了一出舞台剧，很荣幸，我被选了女主角，这段时间每天都在练习，很辛苦呢，好在今天表演的很顺利，没忘词，而且我觉得我表现也挺不错的。今天心情好，给大家拍了一段小视频，看我今天是不是很漂亮呢。现在的我做什么都自信满满的，感觉自己怎样都美美的，告别了之前塌塌丑丑的鼻子，生活也变得越来越多姿多彩了，特别喜欢现在的状态，也特别庆幸自己当初做的选择，特别感谢为我做鼻子的医生，还有恢复期间帮我解答问题的医助小姐姐，没有他们我的鼻子也
            </p>
            <div class="diary-img m-t-20 flex">
              <p class="img-box before-img">
                <span class="tags font-30 c-ff">美前</span>
                <img class="block" src="https://si.meb.com/hospital/20180508/0527c81544404cb79c21246c5b8fb118937-s" alt="">
              </p>
              <p class="img-box after-img">
                <span class="tags font-30 c-ff">美后</span>
                <img class="block" src="https://si.meb.com/hospital/20180705/d2173019ecdf4cafb40ff442fc1aed0c844-s" alt="">
              </p>
              <p class="img-box more-img">
                <span class="diary-num flex flex-column flex-center">
                  <i class="normal c-meb font-50">10</i>
                  <span class="font-30 c-66 p-t-20">篇日记</span>
                </span>
                <img class="block" src="https://m.meb.com/Content/images/home/diary-more.png" alt="">
              </p>
            </div>
            <p class="m-t-20 c-99 font-26 one-line">2019-10-12</p>
          </router-link>
        </ul>
        <div class="bg-f5 p-b-20"></div>
        <div class="paging flex flex-between p-l-30 p-r-30 p-t-20 p-b-20">
          <p class="page-item prev font-30 c-33" :class="{'nomore' : !hasPrev}" @click="getPrevPage">上一页</p>
          <p class="page-num page font-30 c-66">{{filter.page}} / {{totalPage}}</p>
          <p class="page-item next font-30 c-33" :class="{'nomore' : !hasNext}" @click="getNextPage">下一页</p>
        </div>
      </div>
      <div class="nodata flex flex-center flex-column" :style="{height:tabH + 'px'}" v-else v-cloak>
        <img src="../../assets/img/common/pic_null@2x.png" alt="">
        <p class="p-l-30 p-r-30 p-t-60 c-66 font-30">这里空空如也~</p>
      </div>
    </m-center>
  </m-container>
</template>

<script>
//import api from '@/plus/api';
import store from "@/store";
import config from "@/plus/http/config";
export default {
  name: "project.list",
  data() {
    return {
      headType: 2,
      footerType: 0,
      pageStatus: 0,
      host: config.host,
      
      tabH:0,
      selected:-1,//筛选选择
      showMenu:false,//显示筛选列表

      filterData:void 0,//筛选数据
      firstData:void 0,//一级类目
      secondData:void 0,//二级类目

      //分页参数
      filter:{
        page:1,
        spellName:'all',
        sort:0
      },
      navigation:[{
        Name:'全部项目',
        SpellName:'all'
      }],
      hasPrev:false,//是否有上一页
      hasNext:true,//是否有下一页
      totalPage:0,//总页数
      diaryList:void 0,//最新攻略列表
    };
  },
  computed: {},
  created() {
    this.getFilterData();
    this.filter.spellName = this.$route.params.spellname ? this.$route.params.spellname : 'all';
    this.filter.sort  = this.$route.params.parm ? this.$route.params.parm : 0;
    this.filter.page  = this.$route.params.page ? this.$route.params.page : 1;
    this.getDiaryList();
  },
  activated() {},
  deactivated() {},
  mounted() {
    
    this.adJustStyle();
  },
  methods: {
    //判断筛选下拉高度
    adJustStyle() {
      let winH = window.innerHeight;
      let fontSize = fontSize = parseFloat(document.documentElement.style.fontSize.replace('px', ''));
      let navH = document.querySelector('.p-nav').clientHeight;//导航高度
      let filterNav = document.querySelector('.nav-filtrate').clientHeight;//筛选导航高度
      this.tabH = winH - navH - filterNav - (fontSize * 0.88) - 1;
    },
    //切换筛选nav
    tapMenu(index){
      var _this = this;
      if (index != _this.selected) {
        _this.openModal(index);
      } else if (index == _this.selected) {
        _this.closeModal();
      }
    },
    openModal(index){
      this.selected = index;
      this.showMenu = true;
      document.querySelector('.m-center').scrollTo(0,0);
      document.querySelector('.m-center').style.position = 'fixed';
      document.querySelector('.m-center').style.overflow = 'hidden';
      document.querySelector('.m-center').style.width = '100%';
      document.querySelector('.m-center').style.maxWidth = '414px';
    },
    closeModal(){
      this.selected = -1;
      this.showMenu = false;
      document.querySelector('.m-center').style.overflow = 'auto';
      document.querySelector('.m-center').style.position = 'relative';
    },

    //获取筛选数据
    async getFilterData(){
      var _this = this;
      await _this.$http.diary.getDiaryFilter().then( res => {
        if(res.data.Success){
          var data = res.data.Content;
          _this.filterData = data;
          _this.pageStatus = 2;
        }
      }).catch(err=>{
        console.log(err)
      })
    },

    //数据重置
    resetData(){
      var _this = this;
      _this.filter.page = 1;
      _this.filter.pram = 0;
    },

    //选择项目
    chooseProject(name){
      var _this = this;
      _this.filter.sname = name;
      if(name == 'all'){
        _this.firstData = void(0);//一级类目
        _this.secondData = void(0);//二级类目
      }
      _this.$store.dispatch('loadchange',{
        onLoad: true
      })
      _this.resetData();
      _this.getDiaryList();
      _this.closeModal();
      _this.$router.replace({path:`/diary/${_this.filter.sname}/${_this.filter.page}/${_this.filter.parm}`})
    },

    //选择类别
    tapClassical(index){
      var _this = this;
      if(_this.filter.parm == index) return;
      _this.filter.parm = index;
      _this.$store.dispatch('loadchange',{
        onLoad: true
      })
      _this.resetData();
      _this.getDiaryList();
      _this.closeModal();
      _this.$router.replace({path:`/diary/${_this.filter.sname}/${_this.filter.page}/${_this.filter.parm}`})
    },
    //获取最新攻略列表
    async getDiaryList(){
      var _this = this;
      var model = _this.filter;
      if(_this.filter.page > 1) {
        _this.hasPrev = true;
        _this.$store.dispatch('loadchange',{
          onLoad: true
        })
      }else{
        _this.hasPrev = false;
      }
      await _this.$http.diary.getDiaryList(model).then( res => {
        if(res.data.Success){
          var data = res.data.Content;
          _this.diaryList = data.Data;
          _this.totalPage = Math.ceil(data.Count / 10);
          _this.hasNext = _this.filter.page < _this.totalPage ? true : false;
          _this.navigation = data.Navigation;
          if(_this.navigation && _this.navigation.length < 2){
            _this.firstData = _this.navigation[0];
          }else{
            _this.firstData = _this.navigation[0];
            _this.secondData = _this.navigation[1];
          }
          _this.$store.dispatch('loadchange',{
            onLoad: false
          })
        }else{
          _this.$store.dispatch('loadchange',{
            onLoad: false
          })
        }
      }).catch(err=>{
        _this.$store.dispatch('loadchange',{
          onLoad: false
        })
        console.log(err)
      })
    },
    //上一页
    async getPrevPage(){
      var _this = this;
      
      if(_this.filter.page == 1) return;
      else{
        document.querySelector('.m-center').scrollTo(0,0);
        _this.filter.page--;
      }  
      await _this.getDiaryList();
      _this.$router.replace({path:`/diary/${_this.filter.sname}/${_this.filter.page}/${_this.filter.parm}`});
    },
    //下一页
    async getNextPage(){
      var _this = this;
      
      if(_this.filter.page == _this.totalPage) return;
      else {
        document.querySelector('.m-center').scrollTo(0,0);
        _this.filter.page++;
      }
      await _this.getDiaryList();
      _this.$router.replace({path:`/diary/${_this.filter.sname}/${_this.filter.page}/${_this.filter.parm}`});
    },
  }
};
</script>

<style lang="less" scoped>
.icon-right {
  display: block;
  width: 0.24rem;
  height: 0.24rem;
  background: url(../../assets/img/home/icon_more.png) no-repeat center;
  background-size: contain;
  margin: 0 0.1rem;
}
.border-bottom{
  border-bottom:1px solid #f2f2f2;
}
/**筛选数据*/
.filtrate-box{
  position:relative;
  top:0;
  left:0;
  .nav-filtrate{
    position:relative;
    background:#fff;
    z-index:10;
    .filtrate-item{
      padding:0 .1rem;
      flex-shrink: 0;
      flex: 1;
      border-right:1px solid #f0f0f0;
      overflow: hidden;
      &:last-child{
        border-right:none;
      }
      .nav-name{
        display: block;
        color:#373737;
        max-width: calc(100% - 0.4rem);
        text-align: center;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
      &.menu-active{
        .nav-name{
          color:#ff8686;
        }
        .icon-arrow{
          border-right: 0.01rem solid #ff8686;
          border-bottom: 0.01rem solid #ff8686;
          -webkit-transform: rotate(225deg);
          transform: rotate(225deg);
          transform-origin: 50% 85%;
        }
      }
      .icon-arrow{
        flex-shrink: 0;
        display: block;
        top: 50%;
        left: 0;
        width: 0.15rem;
        height: 0.15rem;
        margin-top: -.1rem;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        border-right: 0.01rem solid #333;
        border-bottom: 0.01rem solid #333;
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
        padding: 0;
        margin-left: .1rem;
        transition: all .5s ease;
      }
      .icon-arrow-down{
        display:block;
        flex-shrink: 0;
        width:.24rem;
        height:.24rem;
        background:url(../../assets/img/common/arrow-down.png)no-repeat center;
        background-size:contain;
        margin:0 .1rem;
      }
      .icon-arrow-up{
        display:block;
        flex-shrink: 0;
        width:.24rem;
        height:.24rem;
        background:url(../../assets/img/common/arrow-up.png)no-repeat center;
        background-size:contain;
        margin:0 .1rem;
      }
    }
  }
  .filtrate-container{
    position:absolute;
    left:0;
    right:0;
    bottom:0;
    z-index:10;
    transform: translateY(100%);
    overflow-y: scroll;
    &::-webkit-scrollbar {
      display: none;
    }
    .filtrate-mask{
      position:absolute;
      top:0;
      left:0;
      width:100%;
      height:100%;
      background:rgba(0,0,0,.5);
    }
    .filtrate-data{
      background:#fff;
      position:relative;
      z-index:20;
      .filters{
        padding:.2rem .3rem;
        border-top:1px solid #f0f0f0;
        &.choose{
          color:#ff8686;
          position:relative;
          &:before{
            content:"";
            display:block;
            position:absolute;
            top:0;
            left:0;
            width:.04rem;
            height:100%;
            background:#ff8686;
          }
          &:after{
            content:"";
            display:block;
            position:absolute;
            right:.3rem;
            width:.3rem;
            height:.3rem;
            top: 50%;
            margin-top: -.15rem;
            background:url(../../assets/img/common/p-checked.png);
            background-size:contain;
          }
        }
      }
    }
  }
}
/**结果列表页 */
.result-box{
  .result-item{
    &:last-child{
      border-bottom:none;
    }
    .user-box{
      .user-head{
        width:.88rem;
        height:.88rem;
        border-radius: 50%;
        margin-right:.3rem;
      }
      .user-msg{
        width:calc(100% - .88rem - .3rem);
        min-height: .88rem;
        .tag{
          padding:.05rem .1rem;
          background:#f3f3f3;
          border-radius: .08rem;
        }
      }
    }
    .diary-img{
      .img-box{
        width:calc( (100% - .4rem)/3);
        margin-right:.2rem;
        border-radius: .1rem;
        overflow: hidden;
        position:relative;
        top:0;
        left:0;
        &:nth-child(3n){
          margin-right:0;
        }
        .tags{
          position:absolute;
          left:0;
          bottom:0;
          padding: .02rem .1rem;
          border-top-right-radius: .16rem;
          border-bottom-left-radius: .1rem;
        }
      }
      .before-img{
        .tags{
          background: #0c0908;
          opacity: .9;
        }
      }
      .after-img{
        .tags{
          background: #ff8686;
          opacity: .9;
        }
      }
      .more-img{
        .diary-num{
          position:absolute;
          top:0;
          left:0;
          right:0;
          bottom:0;
        }
      }
    }
  }
}
/**分页 */
.paging{
  p{
    display:block;
    flex:1;
    flex-shrink: 0;
    text-align: center;
    position:relative;
  }
  .page-item:before,.page-item:after{
    content:"";
    display:inline-block;
    top: 0;
    left: 0;
    width: 0.2rem;
    height: 0.2rem;
    box-sizing: border-box;
    transform: rotate(45deg);
    padding: 0;
    margin-left: 0.05rem;
  }
  .prev:before{
    border-left: 0.02rem solid #333;
    border-bottom: 0.02rem solid #333;
  }
  .next:after{
    border-top: 0.02rem solid #333;
    border-right: 0.02rem solid #333;
  }
  .nomore{
    color:#999;
    &.prev:before{
      border-left: 0.02rem solid #999;
      border-bottom: 0.02rem solid #999;
    }
    &.next:after{
      border-top: 0.02rem solid #999;
      border-right: 0.02rem solid #999;
    }
  }
  .page{
    border-left:1px solid #ddd;
    border-right:1px solid #ddd;
  }
}
/**没有数据 */
.nodata{
  img{
    width:2rem;
  }
}
</style>


