<template>
  <m-container>
    <m-header headType="3"></m-header>
    <m-center :pageStatus="pageStatus">
      <div class="m-content animated fadeIn" v-cloak>
        <p class="title font-32 c-33 p-l-30 p-r-30">整形美容大全</p>
        <div class="tab-container" :style="{height:tabH + 'px'}">
          <div class="tab" v-cloak>
            <!-- <div class="tab-cover" v-cloak></div> -->
            <div class="tab-inner flex flex-between flex-top" :style="{height:tabH + 'px'}" v-if="projectList" v-cloak>
              <div class="p-left project-left animated fadeIn">
                <ul class="p-list" v-cloak>
                  <li :class="{'checked' : checkedProject == p.SpellName}" v-for="(p,i) in projectList" :key="i" @click="changeProject(p.SpellName)">{{p.Name}}</li>
                </ul>
              </div>
              <div class="p-right project-right animated fadeIn">
                <ul class="p-child animated" v-cloak v-if="checkedList.length>0">
                  <li class="p-item-list" v-for="(item,index) in checkedList" :key="index">
                    <router-link :to="{path:`/project/z${item.IntId}`}" tag="p" class="font-30">{{item.Name}}</router-link>
                    <ul class="p-child-child flex" v-cloak v-if="item.Childs.length>0">
                      <router-link :to="{path:`/project/z${item.IntId}`}" tag="li" v-for="(child,c) in item.Childs" :key="c">{{child.Name}}</router-link>
                    </ul>
                  </li>
                </ul>
                <ul class="p-child" v-cloak v-else>
                  <li class="p-item-list">
                    <p class="font-30">暂无相关项目</p>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </m-center>
  </m-container>
</template>

<script>
//import api from '@/plus/api';
import store from "@/store";
import config from "@/plus/http/config";
export default {
  name: "index",
  data() {
    return {
      footerType: 0,
      pageStatus: 0,
      host: config.host,
      tabH:0,

      checkedProject:'wzx',//默认选中的项目
      checkedList:void 0,//默认选中的子项目
      projectList:void 0,//项目列表
    };
  },
  computed: {
    
  },
  created(){
    // var SpellName = this.$route.params.spellname;
    // console.log(SpellName);
  },
  activated() {},
  deactivated() {},
  mounted() {
    this.pageStatus = 2;
    this.adJustStyle();
    this.getProject();
  },
  methods: {
    adJustStyle(){
      var bodyH = document.body.clientHeight;
      var headH = document.querySelector('.m-header').clientHeight;
      var titleH = document.querySelector('.title').offsetHeight;
      this.tabH = bodyH - headH - titleH;
    },
    //获取项目列表
    async getProject(){
      var _this = this;
      await _this.$http.project.getProjectData().then(res => {
        if(res.data.Success){
          var data = res.data.Content;
          _this.projectList = data;
          _this.checkedProject = data[0].SpellName;
          _this.checkedList = data[0].Childs;
        }
      }).catch(err => {
        console.log(err)
      })
    },
    //切换项目
    changeProject(name){
      var _this = this;
      document.getElementsByClassName('p-child')[0].classList.remove('fadeIn');
      _this.checkedProject = name;
      var index;
      if(name){
        for(var i=0;i<_this.projectList.length;i++){
          if(name == _this.projectList[i].SpellName) index = i;
        }
      }
      _this.checkedList = _this.projectList[index].Childs;
      document.getElementsByClassName('p-child')[0].classList.add('fadeIn');
    },
  }
};
</script>

<style lang="less" scoped>
.title {
  height:1rem;
  line-height: 1rem;
  border-bottom: 1px solid #f2f2f2;
}
.tab-container {
  position:relative;
  top:0;
  left:0;
  .tab{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    .tab-cover{
      width: 100%;
      height: 100%;
      background: #000;
      opacity: 0.3;
    }
    .tab-inner{
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: #fff;
      .p-left{
        height:100%;
        &.project-left{
          width:2.2rem;
          background:#f5f5f5;
        }
        .p-list{
          width:100%;
          height:100%;
          overflow: hidden;
          overflow-y:scroll;
          &::-webkit-scrollbar {
            display: none;
          }
          li{
            height:.9rem;
            line-height: .9rem;
            text-align: center;
            font-size:.28rem;
            color:#666;
            padding:0 .08rem;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            border-left:.05rem solid transparent;
            &.checked{
              color:#ff8686;
              background:#fff;
              border-left:.05rem solid #ff8686;
            }
          }    
        }
      }
      .p-right{
        height:100%;
        &.project-right{
          width:5.3rem;
          background:#fff;
          padding-left:.2rem;
          .p-child{
            width:100%;
            height:100%;
            padding:.2rem .2rem .2rem 0;
            overflow: hidden;
            overflow-y:scroll;
            &::-webkit-scrollbar {
              display: none;
            }
            .p-item-list{
              width:100%;
              margin-bottom:.2rem;
              p{
                text-align:center;
                background:#f6f6f6;
                padding:.1rem .2rem;
                color:#666;
                border-radius: .4rem;
                margin-bottom:.2rem;
                &:active{
                  color:#ff8686;
                }
              }
              .p-child-child{
                width:100%;
                flex-wrap: wrap;
                justify-content: space-between;
                li{
                  width:45%;
                  font-size:.26rem;
                  color:#666;
                  margin-top:.2rem;
                  &:active{
                    color:#ff8686;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  
}
</style>


