<template>
  <m-container>
    <m-header headType="3"></m-header>
    <m-center :pageStatus="pageStatus">
      <!-- 导航 -->
      <nav class="p-nav p-t-20 p-b-20 p-l-30 p-r-30 c-7f flex border-bottom border-box" v-cloak>
        <router-link to="/" class="font-32">美呗</router-link>
        <i class="icon-right"></i>
        <router-link to="/question" class="font-32">整形问答</router-link>
        <template v-if="firstData">
          <template v-if="firstData && secondData">
            <i class="icon-right"></i>
            <router-link class="font-32" :to="{path:`/news/${firstData.SpellName}`}">{{firstData.Name}}</router-link>
            <i class="icon-right"></i>
            <span class="font-32">{{secondData.Name}}</span>
          </template>
          <template v-else>
            <i class="icon-right"></i>
            <span class="font-32">{{firstData.Name}}</span>
          </template>
        </template>
      </nav>
      <div class="filtrate-box" v-cloak>
        <section class="nav-filtrate flex flex-between p-t-20 p-b-20">
          <nav class="filtrate-item flex flex-center" :class="{'menu-active':selected == 0}" @click="tapMenu(0)">
            <span class="nav-name font-28" v-if="firstData">{{firstData.Name}}</span>
            <span class="nav-name font-28" v-else>部位</span>
            <!-- <i :class="selected == 0 ? 'icon-arrow-up' : 'icon-arrow-down'"></i> -->
            <!-- <i :class="selected == 0 ? 'icon-arrow' : 'icon-arrow'"></i> -->
            <i class="icon-arrow"></i>
          </nav>
          <nav class="filtrate-item flex flex-center" :class="{'menu-active':selected == 1}" @click="tapMenu(1)">
            <span class="nav-name font-28" v-if="secondData">{{secondData.Name}}</span>
            <span class="nav-name font-28" v-else>项目名称</span>
            <!-- <i :class="selected == 1 ? 'icon-arrow-up' : 'icon-arrow-down'"></i> -->
            <i class="icon-arrow"></i>
          </nav>
          <nav class="filtrate-item flex flex-center" :class="{'menu-active':selected == 2}" @click="tapMenu(2)">
            <span class="nav-name font-28" v-if="filter.parm == 0">全部问题</span>
            <span class="nav-name font-28" v-else-if="filter.parm == 1">最新问题</span>
            <span class="nav-name font-28" v-else-if="filter.parm == 2">最新问题</span>
            <span class="nav-name font-28" v-else>名医解答</span>
            <!-- <i :class="selected == 2 ? 'icon-arrow-up' : 'icon-arrow-down'"></i> -->
            <i class="icon-arrow"></i>
          </nav>
        </section>
        <div class="filtrate-container" :style="{height:tabH + 'px'}" v-show="showMenu">
          <div class="filtrate-mask" @click="closeModal"></div>

          <ul class="filtrate-data" v-if="selected == 0">
            <router-link to="" tag="li" class="filters font-30 c-66"
              :class="{'choose' : firstData.SpellName == 'all'}"
              @click.native="chooseProject('all')">全部项目</router-link>
            <router-link to="" tag="li" class="filters font-30 c-66"
              v-for="(item,index) in filterData" :key="index"
              :class="{'choose' : firstData.SpellName == item.SpellName}"
              @click.native="chooseProject(item.SpellName)">{{item.Name}}</router-link>
          </ul>

          <ul class="filtrate-data" v-if="selected == 1">
            <div v-if="firstData">
              <div v-for="(second,i) in filterData" :key="i">
                <template v-show="second.SpellName == firstData.SpellName">
                  <router-link to="" tag="li" class="filters font-30 c-66"
                    v-show="second.SpellName == firstData.SpellName"
                    v-for="items in second.Childs" :key="items.Id"
                    :class="{'choose' : filter.sname == items.SpellName}"
                    @click.native="chooseProject(items.SpellName)"
                    >{{items.Name}}</router-link>
                </template>
              </div>
            </div>
            <router-link v-if="filter.sname == 'all'" to="" tag="li" class="filters font-30 c-66 choose" replace>全部</router-link>
          </ul>

          <ul class="filtrate-data" v-if="selected == 2">
            <router-link to="" tag="li" class="filters font-30 c-66" :class="{'choose' : filter.parm == 0 }" @click.native="tapClassical(0)">全部问题</router-link>
            <router-link to="" tag="li" class="filters font-30 c-66" :class="{'choose' : filter.parm == 1 }" @click.native="tapClassical(1)">最新问题</router-link>
            <router-link to="" tag="li" class="filters font-30 c-66" :class="{'choose' : filter.parm == 2 }" @click.native="tapClassical(2)">最热问题</router-link>
            <router-link to="" tag="li" class="filters font-30 c-66" :class="{'choose' : filter.parm == 2 }" @click.native="tapClassical(3)">名医解答</router-link>
          </ul>
        </div>
      </div>
      <div class="result-box" v-if="newsList" v-cloak>
        <div class="bg-f5 p-b-20"></div>
        <ul class="result-list p-l-30 p-r-30">
          <router-link tag="li" class="result-item p-t-30 p-b-30"
            v-for="(item,i) in newsList" :key="i"
            :to="{path:`/question/${item.StateShowID}.html`}">
            <p class="c-33 font-32 flex flex-top" v-if="item.Title">
              <i class="icon-question"></i>
              <span class="question-title two-line font-32 c-33">{{item.Title}}</span>
            </p>
            <p class="p-t-20 font-32 flex flex-top" v-if="item.AnswerContent">
              <i class="icon-answer"></i>
              <span class="question-title three-line font-32 c-99"><span class="answer-text" v-html="item.AnswerContent"></span></span>
            </p>
          </router-link>
        </ul>
        <div class="bg-f5 p-b-20"></div>
        <div class="paging flex flex-between p-l-30 p-r-30 p-t-20 p-b-20">
          <p class="page-item prev font-30 c-33" :class="{'nomore' : !hasPrev}" @click="getPrevPage">上一页</p>
          <p class="page-num page font-30 c-66">{{filter.page}} / {{totalPage}}</p>
          <p class="page-item next font-30 c-33" :class="{'nomore' : !hasNext}" @click="getNextPage">下一页</p>
        </div>
      </div>
      <div class="nodata flex flex-center flex-column" :style="{height:tabH + 'px'}" v-else v-cloak>
        <img src="../../assets/img/common/pic_null@2x.png" alt="">
        <p class="p-l-30 p-r-30 p-t-60 c-66 font-30">这里空空如也~</p>
      </div>
    </m-center>
  </m-container>
</template>

<script>
//import api from '@/plus/api';
import store from "@/store";
import config from "@/plus/http/config";
export default {
  name: "question.list",
  data() {
    return {
      footerType: 0,
      pageStatus: 0,
      host: config.host,
      
      tabH:0,
      selected:-1,//筛选选择
      showMenu:false,//显示筛选列表

      filterData:void 0,//筛选数据
      firstData:void 0,//一级类目
      secondData:void 0,//二级类目

      //分页参数
      filter:{
        page:1,
        sname:'all',
        parm:0
      },
      navigation:[{
        Name:'全部问题',
        SpellName:'all'
      }],
      hasPrev:false,//是否有上一页
      hasNext:true,//是否有下一页
      totalPage:0,//总页数
      newsList:void 0,//最新攻略列表
    };
  },
  computed: {},
  created() {
    this.getFilterData();
    this.filter.sname = this.$route.params.spellname ? this.$route.params.spellname : 'all';
    this.filter.parm  = this.$route.params.parm ? this.$route.params.parm : 0;
    this.filter.page  = this.$route.params.page ? this.$route.params.page : 1;
    this.getQuestionList();
  },
  activated() {},
  deactivated() {},
  mounted() {
    
    this.adJustStyle();
  },
  methods: {
    //判断筛选下拉高度
    adJustStyle() {
      let winH = window.innerHeight;
      let fontSize = fontSize = parseFloat(document.documentElement.style.fontSize.replace('px', ''));
      let navH = document.querySelector('.p-nav').clientHeight;//导航高度
      let filterNav = document.querySelector('.nav-filtrate').clientHeight;//筛选导航高度
      this.tabH = winH - navH - filterNav - (fontSize * 0.88) - 1;
    },
    //切换筛选nav
    tapMenu(index){
      var _this = this;
      if (index != _this.selected) {
        _this.openModal(index);
      } else if (index == _this.selected) {
        _this.closeModal();
      }
    },
    openModal(index){
      this.selected = index;
      this.showMenu = true;
      document.querySelector('.m-center').scrollTo(0,0);
      document.querySelector('.m-center').style.position = 'fixed';
      document.querySelector('.m-center').style.overflow = 'hidden';
      document.querySelector('.m-center').style.width = '100%';
      document.querySelector('.m-center').style.maxWidth = '414px';
    },
    closeModal(){
      this.selected = -1;
      this.showMenu = false;
      document.querySelector('.m-center').style.overflow = 'auto';
      document.querySelector('.m-center').style.position = 'relative';
    },

    //获取筛选数据
    async getFilterData(){
      var _this = this;
      await _this.$http.question.getQuestionFilter().then( res => {
        if(res.data.Success){
          var data = res.data.Content;
          _this.filterData = data;
          _this.pageStatus = 2;
        }
      }).catch(err=>{
        console.log(err)
      })
    },

    //数据重置
    resetData(){
      var _this = this;
      _this.filter.page = 1;
      _this.filter.pram = 0;
    },

    //选择项目
    chooseProject(name){
      var _this = this;
      _this.filter.sname = name;
      if(name == 'all'){
        _this.firstData = void(0);//一级类目
        _this.secondData = void(0);//二级类目
      }
      _this.$store.dispatch('loadchange',{
        onLoad: true
      })
      _this.resetData();
      _this.getQuestionList();
      _this.closeModal();
      _this.$router.replace({path:`/question/${_this.filter.sname}/${_this.filter.page}/${_this.filter.parm}`})
    },

    //选择类别
    tapClassical(index){
      var _this = this;
      if(_this.filter.parm == index) return;
      _this.filter.parm = index;
      _this.$store.dispatch('loadchange',{
        onLoad: true
      })
      _this.resetData();
      _this.getQuestionList();
      _this.closeModal();
      _this.$router.replace({path:`/question/${_this.filter.sname}/${_this.filter.page}/${_this.filter.parm}`})
    },
    //获取最新攻略列表
    async getQuestionList(){
      var _this = this;
      var model = _this.filter;
      if(_this.filter.page > 1) {
        _this.hasPrev = true;
        _this.$store.dispatch('loadchange',{
          onLoad: true
        })
      }else{
        _this.hasPrev = false;
      }
      await _this.$http.question.getQuestionList(model).then( res => {
        if(res.data.Success){
          var data = res.data.Content;
          _this.newsList = data.Data;
          _this.totalPage = Math.ceil(data.Count / 10);
          _this.hasNext = _this.filter.page < _this.totalPage ? true : false;
          _this.navigation = data.Navigation;
          if(_this.navigation && _this.navigation.length < 2){
            _this.firstData = _this.navigation[0];
          }else{
            _this.firstData = _this.navigation[0];
            _this.secondData = _this.navigation[1];
          }
          _this.$store.dispatch('loadchange',{
            onLoad: false
          })
        }else{
          _this.$store.dispatch('loadchange',{
            onLoad: false
          })
        }
      }).catch(err=>{
        _this.$store.dispatch('loadchange',{
          onLoad: false
        })
        console.log(err)
      })
    },
    //上一页
    async getPrevPage(){
      var _this = this;
      
      if(_this.filter.page == 1) return;
      else{
        document.querySelector('.m-center').scrollTo(0,0);
        _this.filter.page--;
      }  
      await _this.getQuestionList();
      _this.$router.replace({path:`/question/${_this.filter.sname}/${_this.filter.page}/${_this.filter.parm}`});
    },
    //下一页
    async getNextPage(){
      var _this = this;
      
      if(_this.filter.page == _this.totalPage) return;
      else {
        document.querySelector('.m-center').scrollTo(0,0);
        _this.filter.page++;
      }
      await _this.getQuestionList();
      _this.$router.replace({path:`/question/${_this.filter.sname}/${_this.filter.page}/${_this.filter.parm}`});
    },
  }
};
</script>

<style lang="less" scoped>
.icon-right {
  display: block;
  width: 0.24rem;
  height: 0.24rem;
  background: url(../../assets/img/home/icon_more.png) no-repeat center;
  background-size: contain;
  margin: 0 0.1rem;
}
.icon-question{
  display: block;
  flex-shrink: 0;
  width: 0.4rem;
  height: 0.4rem;
  margin-right: 0.15rem;
  background: url(https://m.meb.com/Content/images/question/now-wen.png) no-repeat center;
  background-size: contain;
}
.icon-answer{
  display: block;
  flex-shrink: 0;
  width: 0.4rem;
  height: 0.4rem;
  margin-right: 0.15rem;
  background: url(https://m.meb.com/Content/images/question/now-da.png) no-repeat center;
  background-size: contain;
}
.border-bottom{
  border-bottom:1px solid #f2f2f2;
}
/**筛选数据*/
.filtrate-box{
  position:relative;
  top:0;
  left:0;
  .nav-filtrate{
    position:relative;
    background:#fff;
    z-index:10;
    .filtrate-item{
      padding:0 .1rem;
      flex-shrink: 0;
      flex: 1;
      border-right:1px solid #f0f0f0;
      overflow: hidden;
      &:last-child{
        border-right:none;
      }
      .nav-name{
        display: block;
        color:#373737;
        max-width: calc(100% - 0.4rem);
        text-align: center;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
      &.menu-active{
        .nav-name{
          color:#ff8686;
        }
        .icon-arrow{
          border-right: 0.01rem solid #ff8686;
          border-bottom: 0.01rem solid #ff8686;
          -webkit-transform: rotate(225deg);
          transform: rotate(225deg);
          transform-origin: 50% 85%;
        }
      }
      .icon-arrow{
        flex-shrink: 0;
        display: block;
        top: 50%;
        left: 0;
        width: 0.15rem;
        height: 0.15rem;
        margin-top: -.1rem;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        border-right: 0.01rem solid #333;
        border-bottom: 0.01rem solid #333;
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
        padding: 0;
        margin-left: .1rem;
        transition: all .5s ease;
      }
      .icon-arrow-down{
        display:block;
        flex-shrink: 0;
        width:.24rem;
        height:.24rem;
        background:url(../../assets/img/common/arrow-down.png)no-repeat center;
        background-size:contain;
        margin:0 .1rem;
      }
      .icon-arrow-up{
        display:block;
        flex-shrink: 0;
        width:.24rem;
        height:.24rem;
        background:url(../../assets/img/common/arrow-up.png)no-repeat center;
        background-size:contain;
        margin:0 .1rem;
      }
    }
  }
  .filtrate-container{
    position:absolute;
    left:0;
    right:0;
    bottom:0;
    z-index:10;
    transform: translateY(100%);
    overflow-y: scroll;
    &::-webkit-scrollbar {
      display: none;
    }
    .filtrate-mask{
      position:absolute;
      top:0;
      left:0;
      width:100%;
      height:100%;
      background:rgba(0,0,0,.5);
    }
    .filtrate-data{
      background:#fff;
      position:relative;
      z-index:20;
      .filters{
        padding:.2rem .3rem;
        border-top:1px solid #f0f0f0;
        &.choose{
          color:#ff8686;
          position:relative;
          &:before{
            content:"";
            display:block;
            position:absolute;
            top:0;
            left:0;
            width:.04rem;
            height:100%;
            background:#ff8686;
          }
          &:after{
            content:"";
            display:block;
            position:absolute;
            right:.3rem;
            width:.3rem;
            height:.3rem;
            top: 50%;
            margin-top: -.15rem;
            background:url(../../assets/img/common/p-checked.png);
            background-size:contain;
          }
        }
      }
    }
  }
}
/**结果列表页 */
.result-box{
  .result-item{
    border-bottom:1px solid #f2f2f2;
    &:last-child{
      border-bottom: none;
    }
  }
}
/**分页 */
.paging{
  p{
    display:block;
    flex:1;
    flex-shrink: 0;
    text-align: center;
    position:relative;
  }
  .page-item:before,.page-item:after{
    content:"";
    display:inline-block;
    top: 0;
    left: 0;
    width: 0.2rem;
    height: 0.2rem;
    box-sizing: border-box;
    transform: rotate(45deg);
    padding: 0;
    margin-left: 0.05rem;
  }
  .prev:before{
    border-left: 0.02rem solid #333;
    border-bottom: 0.02rem solid #333;
  }
  .next:after{
    border-top: 0.02rem solid #333;
    border-right: 0.02rem solid #333;
  }
  .nomore{
    color:#999;
    &.prev:before{
      border-left: 0.02rem solid #999;
      border-bottom: 0.02rem solid #999;
    }
    &.next:after{
      border-top: 0.02rem solid #999;
      border-right: 0.02rem solid #999;
    }
  }
  .page{
    border-left:1px solid #ddd;
    border-right:1px solid #ddd;
  }
}
/**没有数据 */
.nodata{
  img{
    width:2rem;
  }
}
</style>

<style lang="css">
  .answer-text p,.answer-text span{
    font-size:.3rem;
  }
</style>


