<template>
  <m-container>
    <m-header headType="3"></m-header>
    <m-center :pageStatus="pageStatus">
      <!-- 整形攻略分类 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30" v-cloak>
        <div class="box-title flex">
          <i class="icon-title"></i>
          <p class="font-34">整形攻略分类</p>
        </div>
        <div class="wrap-box p-t-30 strategy-classify">
          <ul class="strategy-list flex flex-wrap hot-tag">
            <router-link tag="li" class="strategy-item hot-tag-item font-26 one-line" :to="{path:`/news/wzx`}">微整注射</router-link>
            <router-link tag="li" class="strategy-item hot-tag-item font-26 one-line" :to="{path:`/news/ybzx`}">眼部整形</router-link>
            <router-link tag="li" class="strategy-item hot-tag-item font-26 one-line" :to="{path:`/news/bbzx`}">鼻部整形</router-link>
            <router-link tag="li" class="strategy-item hot-tag-item font-26 one-line" :to="{path:`/news/xbzx`}">胸部整形</router-link>
            <router-link tag="li" class="strategy-item hot-tag-item font-26 one-line" :to="{path:`/news/mblk`}">面部轮廓</router-link>
            <router-link tag="li" class="strategy-item hot-tag-item font-26 one-line" :to="{path:`/news/zfzx`}">脂肪整形</router-link>
            <router-link tag="li" class="strategy-item hot-tag-item font-26 one-line" :to="{path:`/news/pfmr`}">皮肤美容</router-link>
            <router-link tag="li" class="strategy-item hot-tag-item font-26 one-line" :to="{path:`/news/mbzx`}">眉部整形</router-link>
            <router-link tag="li" class="strategy-item hot-tag-item font-26 one-line" :to="{path:`/news/mfmr`}">毛发美容</router-link>
            <router-link tag="li" class="strategy-item hot-tag-item font-26 one-line" :to="{path:`/news/kczx`}">口唇整形</router-link>
            <router-link tag="li" class="strategy-item hot-tag-item font-26 one-line" :to="{path:`/news/yczx`}">牙齿整形</router-link>
            <router-link tag="li" class="strategy-item hot-tag-item font-26 one-line" :to="{path:`/news/ebzx`}">耳部整形</router-link>
          </ul>
          <router-link :to="{path:`/news/all`}" tag="div" class="look-more flex flex-center">
            <img src="../../assets/img/common/more.png" alt />
            <span class="font-28 c-99">查看全部攻略</span>
          </router-link>
        </div>
      </div>
      <div class="bg-f5 p-b-20"></div>
      <!-- 最新攻略 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30" v-cloak v-if="newsList">
        <div class="box-title flex">
          <i class="icon-title"></i>
          <p class="font-34">最新攻略</p>
        </div>
        <div class="wrap-box strategy-box">
          <router-link :to="{path:`/news/${news.StateShowID}.html`}" tag="div" 
            class="strategy border-bottom p-t-30 p-b-30"
            v-for="(news,index) in newsList" :key="index">
            <p class="two-line c-33 font-32">
              <span class="strategy-title font-32 c-66">{{news.Title}}</span>
            </p>
            <p class="p-t-20">
              <span class="doc-tag font-22" v-if="news.IsProfessor">专家</span>
              <span class="font-24 c-66">{{news.DoctorName}}</span>
              <span class="font-24 c-b2">/ {{news.HospitalName}} /</span>
              <span class="font-24 c-b2">{{news.JobTitle}}</span>
            </p>
          </router-link>
          <router-link to tag="div" class="look-more flex flex-center p-t-30">
            <img src="../../assets/img/common/more.png" alt />
            <span class="font-28 c-99">查看全部攻略</span>
          </router-link>
        </div>
      </div>
    </m-center>
  </m-container>
</template>

<script>
//import api from '@/plus/api';
import store from "@/store";
import config from "@/plus/http/config";
export default {
  name: "project.index",
  data() {
    return {
      footerType: 0,
      pageStatus: 0,
      host: config.host,
      
      newsList:void 0,//最新攻略列表
    };
  },
  computed: {},
  created() {
    this.getNews();
  },
  activated() {},
  deactivated() {},
  mounted() {
    this.pageStatus = 2;
  },
  methods: {
    //获取最新攻略
    async getNews(){
      var _this = this;
      await _this.$http.news.getData().then(res => {
        if(res.data.Success){
          _this.newsList = res.data.Content;
        }
      }).catch(err=>{
        console.log(err)
      })
    }
  }
};
</script>

<style lang="less" scoped>
.head-left {
  height: 0.46rem;
  width: 1rem;
  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}
.head-right {
  .search {
    height: 0.6rem;
    width: 5.6rem;
    background: #f1f1f1;
    border-radius: 1rem;
    padding: 0 0.2rem;
    .search-tip {
      color: #666;
    }
    img {
      width: 0.46rem;
      height: 0.46rem;
      object-fit: contain;
    }
  }
}

.icon-title {
  display: block;
  width: 0.06rem;
  height: 0.34rem;
  margin-right: 0.15rem;
  background: url(../../assets/img/common/doc.png) no-repeat center;
}

.wrap-box {
  .border-bottom {
    border-bottom: 1px solid #f2f2f2;
  }
  //攻略分类
  .hot-tag {
    .hot-tag-item{
      display:block;
      width: 1.5rem;
      color:#4d4d4d;
      background:#f3f3f3;
      text-align: center;
      padding:.1rem;
      border-radius: .24rem;
      margin-right:.3rem;
      margin-bottom:.2rem;
      &:nth-child(4n){
        margin-right:0;
      }
    }
  }
  // 攻略
  .strategy {
    .strategy-title {
      width:100%;
    }
    .doc-tag{
      background:#a8d8f2;
      color:#fff;
      border-radius: .08rem;
      padding:0 .1rem;
    }
  }
  // 查看更多
  .look-more {
    img {
      width: 0.4rem;
      height: 0.4rem;
      margin-right: 0.12rem;
    }
  }
}
</style>


