<template>
    <div class="err-page">
        <div class="err-content">
            <img class="err-img" src="../../assets/img/error/error-title.png" alt="">
            <div class="err-btn">
                <router-link :to="{name:'home'}" replace>
                    <button class="gohome">回到首页</button>
                </router-link>
                <button class="goback" @click="back">返回上一页</button>
            </div>
        </div>
        <img class="err-footer" src="../../assets/img/error/foot.png" alt="">
    </div>
</template>

<script>
export default{
    data(){
        return{

        }
    },
    methods:{
        back(){
            this.$router.go(-1);
        }
    }
}
</script> 

<style lang="less" scoped>
.err-page{
    width:100%;
    height:100%;
    background:url(../../assets/img/error/bg.png) no-repeat center;
    background-size:cover;
    position:relative;
    .err-content{
        text-align:center;
        padding-top: calc( 40vh - 2rem);
        .err-img{
            width:4rem;
            height:4rem;
            object-fit: contain;
        }
        .err-btn{
            button{
                padding:.1rem .2rem;
                border-radius: .1rem;
                margin:0 .2rem;
                &.gohome{
                    background:#ff8686;
                    color:#fff;
                }
            }
        }
    }
    .err-footer{
        display:block;
        position:absolute;
        left:0;
        bottom:0;
        right:0;
    }
}
</style>


