<template>
  <m-container>
    <m-header headType="3"></m-header>
    <m-center :pageStatus="pageStatus" class="animated fadeIn">
      <!-- 导航 -->
      <nav class="p-nav p-t-20 p-b-20 p-l-30 p-r-30 c-7f flex" v-cloak>
        <router-link to="/" tag="span" class="font-32">美呗</router-link>
        <i class="icon-right"></i>
        <router-link to="/project" tag="span" class="font-32">整形项目</router-link>
        <template v-if="projectMsg && projectMsg.Level == 2">
          <i class="icon-right"></i>
          <span class="font-32">{{projectMsg.Name}}</span>
        </template>
        <template v-if="projectMsg && projectMsg.Level == 3">
          <i class="icon-right"></i>
          <span class="font-32">{{parentProject.Name}}</span>
          <i class="icon-right"></i>
          <span class="font-32">{{projectMsg.Name}}</span>
        </template>
      </nav>
      <div class="bg-f5 p-b-20"></div>
      <!-- 内容 -->
      <div class="p-wrap p-t-30 p-b-30 p-l-30 p-r-30" v-if="projectMsg" v-cloak>
        <p class="p-title font-36 c-66">{{projectMsg.Name}}</p>
        <div class="p-intro m-t-40">
          <img class="p-bg" v-lazy="projectMsg.Image" alt />
          <span class="p-tip c-ff font-32">术后效果</span>
        </div>
        <div class="wrap p-desc-box">
          <div
            class="p-desc-item border-bottom p-t-30 p-b-30"
            v-if="projectMsg && projectMsg.ShortDescription"
          >
            <div class="flex m-b-20">
              <span class="p-desc-tag bk font-26">百科</span>
              <span class="font-32 p-desc-title one-line">什么是{{projectMsg.Name}}？</span>
            </div>
            <div class="flex flex-between">
              <span
                class="p-desc-content font-24 one-line c-99"
                v-html="projectMsg.ShortDescription"
              >{{projectMsg.ShortDescription}}</span>
              <router-link
                :to="{path:`/project/${projectMsg.IntId}`}"
                class="p-lookmore font-24"
                tag="span"
              >[查看更多]</router-link>
            </div>
          </div>
          <div class="p-desc-item p-t-30" v-if="projectMsg && projectMsg.AestheticStandard">
            <div class="flex m-b-20">
              <span class="p-desc-tag mx font-26">美学</span>
              <span class="font-32 p-desc-title one-line">美学标准</span>
            </div>
            <div class="flex flex-between">
              <span
                class="p-desc-content font-24 one-line c-99"
                v-html="projectMsg.AestheticStandard"
              >{{projectMsg.AestheticStandard}}</span>
              <router-link class="p-lookmore font-24" tag="span" to>[了解更多]</router-link>
            </div>
          </div>
        </div>
      </div>
      <div class="bg-f5 p-b-20"></div>
      <!-- 相关特价商品 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30" v-cloak v-if="relatedproblem">
        <div class="box-title flex">
          <i class="icon-title"></i>
          <p class="font-34">相关特价商品</p>
        </div>
        <div class="wrap-box project-box">
          <router-link to tag="div" class="recommend-product border-bottom p-t-30 p-b-30 flex">
            <div class="product-img">
              <img
                src="https://cdn-ssl.meb.com/other/20190814/9140899e8a9d4ea7bd87b182f259cdb8995.jpg-m"
                alt
              />
            </div>
            <div class="recommend-desc flex flex-column">
              <h1 class="product-name font-34 two-line">【黑脸娃娃】①解决毛孔粗大②皮肤暗黄③油腻 网红首选</h1>
              <p class="product-desc font-28 c-99 one-line">成都高新美极医疗美容门诊部</p>
              <p class="product-price c-aa one-line">
                <span class="p-r-30 showPrice font-34 bold c-meb">￥99</span>
                <span class="through">￥398</span>
              </p>
            </div>
          </router-link>
          <router-link to tag="div" class="recommend-product border-bottom p-t-30 p-b-30 flex">
            <div class="product-img">
              <img
                src="https://cdn-ssl.meb.com/other/20190702/d235ce4c88104593be1dc5ad0ccc8eda134.jpg-m"
                alt
              />
            </div>
            <div class="recommend-desc flex flex-column">
              <h1 class="product-name font-34 two-line">【黑脸娃娃】①解决毛孔粗大②皮肤暗黄③油腻 网红首选</h1>
              <p class="product-desc font-28 c-99 one-line">成都高新美极医疗美容门诊部</p>
              <p class="product-price c-aa one-line">
                <span class="p-r-30 showPrice font-34 bold c-meb">￥99</span>
                <span class="through">￥398</span>
              </p>
            </div>
          </router-link>
          <router-link to tag="div" class="look-more flex flex-center p-t-30">
            <img src="../../assets/img/common/more.png" alt />
            <span class="font-28 c-99">了解更多</span>
          </router-link>
        </div>
      </div>
      <div class="bg-f5 p-b-20"></div>
      <!-- 常见问题 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30" v-cloak v-if="relatedproblem">
        <div class="box-title flex">
          <i class="icon-title"></i>
          <p class="font-34">常见问题</p>
        </div>
        <div class="wrap-box question-box">
          <!-- <router-link to tag="div" class="question border-bottom p-t-30 p-b-30">
            <p class="question-title one-line c-33 font-32">成都黑脸娃娃多少钱</p>
            <div class="question-data one-line m-t-20 font-28 flex flex-between">
              <p class="data-number one-line c-b2">
                <span class="c-meb">10</span>名专家回复
                <span class="p-l-10 p-r-10">·</span>
                <span class="c-meb">898</span>
                <span>人关注</span>
              </p>
              <span class="meb-recommend flex c-b2 font-24">
                <img src="../../assets/img/common/tuijian.png" alt />美呗推荐
              </span>
            </div>
          </router-link> -->
          <router-link tag="div" class="question border-bottom p-t-30 p-b-30" v-for="(question,index) in relatedproblem" :key="index" :to="{}">
            <p class="question-title one-line c-33 font-32">{{question.Title}}</p>
            <p class="question-data one-line m-t-20 c-b2 font-28">
              <span class="c-meb">{{question.AnswerCount}}</span>名专家回复
              <span class="p-l-10 p-r-10">·</span>
              <span class="c-meb">{{question.ViewCount}}</span>
              <span>人关注</span>
            </p>
          </router-link>
          <router-link to tag="div" class="look-more flex flex-center p-t-30">
            <img src="../../assets/img/common/more.png" alt />
            <span class="font-28 c-99">了解更多</span>
          </router-link>
        </div>
      </div>
      <div class="bg-f5 p-b-20"></div>
      <!-- 攻略 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30" v-cloak v-if="relatedstrategy">
        <div class="box-title flex">
          <i class="icon-title"></i>
          <p class="font-34">攻略</p>
        </div>
        <div class="wrap-box strategy-box">
          <router-link tag="div" class="strategy border-bottom p-t-30 p-b-30" 
            v-for="(strategy,index) in relatedstrategy" :key="index" 
            :to="{path:`/news/${strategy.IntId}.html`}">
            <p class="one-line c-33 font-32 flex">
              <i class="icon-q"></i>
              <span class="strategy-title one-line font-32 c-66">{{strategy.Title}}</span>
            </p>
          </router-link>
          <router-link to tag="div" class="look-more flex flex-center p-t-30">
            <img src="../../assets/img/common/more.png" alt />
            <span class="font-28 c-99">了解更多</span>
          </router-link>
        </div>
      </div>
      <div class="bg-f5 p-b-20"></div>
      <!-- 知识 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30" v-cloak v-if="reletedknowledge">
        <div class="box-title flex">
          <i class="icon-title"></i>
          <p class="font-34">知识</p>
        </div>
        <div class="wrap-box knowledge-box">
          <router-link tag="div" class="strategy border-bottom p-t-30 p-b-30"
            v-for="(knowledge,index) in reletedknowledge" :key="index" 
            :to="{path:`/news/${knowledge.IntId}.html`}">
            <p class="one-line c-33 font-32 flex">
              <i class="icon-q"></i>
              <span class="strategy-title one-line font-32 c-66">{{knowledge.Title}}</span>
            </p>
          </router-link>
          <router-link to tag="div" class="look-more flex flex-center p-t-30">
            <img src="../../assets/img/common/more.png" alt />
            <span class="font-28 c-99">了解更多</span>
          </router-link>
        </div>
      </div>
      <div class="bg-f5 p-b-20"></div>
      <!-- 视频 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30" v-cloak v-if="relatedvideo && relatedvideo.length>0">
        <div class="box-title flex">
          <i class="icon-title"></i>
          <p class="font-34">视频</p>
        </div>
        <div class="wrap-box video-box">
          <router-link tag="div" class="video border-bottom p-t-30 p-b-30" 
            v-for="(video,index) in relatedvideo" :key="index"
            to="">
            <div class="video-cover">
              <img class="video" v-lazy="video.ImageUrl" alt="">
              <i class="icon-play play-btn"></i>
              <p class="video-title font-30 c-66 one-line">{{video.Title}}</p>
            </div>
          </router-link>
          <router-link to tag="div" class="look-more flex flex-center p-t-30">
            <img src="../../assets/img/common/more.png" alt />
            <span class="font-28 c-99">了解更多</span>
          </router-link>
        </div>
      </div>
      <div class="bg-f5 p-b-20" v-if="relatedvideo && relatedvideo.length>0"></div>
      <!-- 日记案例 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30" v-cloak v-if="reletedknowledge">
        <div class="box-title flex">
          <i class="icon-title"></i>
          <p class="font-34">整形日记</p>
        </div>
        <div class="wrap-box dairy-box">
          <router-link tag="div" class="dairy border-bottom p-t-30 p-b-30"
            to="">
            <div class="flex flex-top">
              <img class="user-head" src="https://cdn-ssl.meb.com/hospital/20180619/993bcfca24c64db3b7c29176e7bf97a3143-s" alt="">
              <div class="flex flex-column flex-top user-msg">
                <p class="font-32 c-33 two-line">我的瘦大腿日记</p>
                <p class="diary-tags m-t-10"><span class="tags font-24 c-66 m-r-10"><i class="c-meb normal">#</i>瘦大腿</span><span class="tags font-24 c-66 m-r-10"><i class="c-meb normal">#</i>术后38天</span></p>
              </div>
            </div>
            <div class="flex flex-between m-t-20 diary-effect">
              <div class="effect before">
                <img src="https://si.meb.com/hospital/20180619/9bbc82cc09c64cfdb5fc9d11a6abb2b8856-s" alt="">
                <span class="font-32">美前</span>
              </div>
              <div class="effect after">
                <img src="https://si.meb.com/hospital/20180627/1e80a510f0ae48c1afeb2a65042fb5d1248-s" alt="">
                <span class="font-32">美后</span>
              </div>
            </div>
            <p class="m-t-20 font-26 c-b2">2018-06-27 13:55:00</p>
          </router-link>
          <router-link tag="div" class="dairy border-bottom p-t-30 p-b-30"
            to="">
            <div class="flex flex-top">
              <img class="user-head" src="https://cdn-ssl.meb.com/hospital/20180619/993bcfca24c64db3b7c29176e7bf97a3143-s" alt="">
              <div class="flex flex-column flex-top user-msg">
                <p class="font-32 c-33 two-line">我的瘦大腿日记</p>
                <p class="diary-tags m-t-10"><span class="tags font-24 c-66 m-r-10"><i class="c-meb normal">#</i>瘦大腿</span><span class="tags font-24 c-66 m-r-10"><i class="c-meb normal">#</i>术后38天</span></p>
              </div>
            </div>
            <div class="flex flex-between m-t-20 diary-effect">
              <div class="effect before">
                <img src="https://si.meb.com/hospital/20180619/9bbc82cc09c64cfdb5fc9d11a6abb2b8856-s" alt="">
                <span class="font-32">美前</span>
              </div>
              <div class="effect after">
                <img src="https://si.meb.com/hospital/20180627/1e80a510f0ae48c1afeb2a65042fb5d1248-s" alt="">
                <span class="font-32">美后</span>
              </div>
            </div>
            <p class="m-t-20 font-26 c-b2">2018-06-27 13:55:00</p>
          </router-link>
          <router-link to tag="div" class="look-more flex flex-center p-t-30">
            <img src="../../assets/img/common/more.png" alt />
            <span class="font-28 c-99">更多案例</span>
          </router-link>
        </div>
      </div>
    </m-center>
  </m-container>
</template>

<script>
//import api from '@/plus/api';
import store from "@/store";
import config from "@/plus/http/config";
export default {
  name: "project.zdetail",
  data() {
    return {
      footerType: 0,
      pageStatus: 0,
      host: config.host,
      tabH: 0,

      projectId: void 0, //项目intid
      projectMsg: void 0, //项目信息
      firstProject: void 0, //第一个项目信息
      parentProject: void 0, //当前项目父元素信息

      relatedproblem:void 0,//相关问题
      relatedstrategy:void 0,//相关攻略
      reletedknowledge:void 0,//相关知识
      relatedvideo:void 0,//相关视频

    };
  },
  computed: {},
  created() {
    var id = this.$route.params.id;
    this.projectId = id.replace(/[a-zA-Z]/g, "");
    this.getProjectMsg();
    //this.getDiary();
  },
  activated() {},
  deactivated() {},
  mounted() {
    this.pageStatus = 2;
    this.adJustStyle();
  },
  methods: {
    adJustStyle() {},
    //获取项目列表
    async getProjectMsg() {
      var _this = this;
      var model = {
        id: _this.projectId
      };
      await _this.$http.project
        .getProjectMsg(model)
        .then(res => {
          if (res.data.Success) {
            var data = res.data.Content;
            var spellName = data.Project.SpellName;
            _this.getRelative(spellName);
            _this.projectMsg = data.Project;
            _this.firstProject = data.FirstProject;
            _this.parentProject = data.ParentProject;
          }
        })
        .catch(err => {
          //_this.$router.replace('/error')
          console.log(err);
        });
    },
    //获取相关数据
    async getRelative(name) {
      var _this = this;
      var model = {
        sname: name,
        questionSize: 5,
        newsSize: 5,
        articleSize: 5,
        videoSize: 1
      };
      await _this.$http.project
        .getProjectRelative(model)
        .then(res => {
          if(res.data.Success){
            var data = res.data.Content;
            _this.relatedproblem = data.questions;
            _this.relatedstrategy = data.news;
            _this.reletedknowledge = data.article;
            _this.relatedvideo = data.video;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },

    //获取相关日记案例
    async getDiary(){
      var _this = this;
      var model = {
        projectId:'545df719-1072-49cc-b935-ab65ecdca0e8',
        page:1,
        size:2
      }
      await _this.$http.project.getDiaryData(model).then( res => {
        if(res.data.Success){
          var data = res.data.Content;
          console.log(data);
        }
      }).catch( err =>{
        console.log(err);
      })
    }
  }
};
</script>

<style lang="less" scoped>
.icon-right {
  display: block;
  width: 0.24rem;
  height: 0.24rem;
  background: url(../../assets/img/home/icon_more.png) no-repeat center;
  background-size: contain;
  margin: 0 0.1rem;
}

.p-intro {
  position: relative;
  text-align: center;
  .p-bg {
    display: block;
    width: 100%;
    height: 3.2rem;
    object-fit: cover;
    border-radius: 0.1rem;
  }
  .p-tip {
    position: absolute;
    bottom: 0.1rem;
    left: 50%;
    transform: translateX(-50%);
  }
}
.p-desc-item {
  &.border-bottom {
    border-bottom: 1px solid #f2f2f2;
  }
  .p-desc-tag {
    width: 0.64rem;
    height: 0.32rem;
    line-height: 0.32rem;
    margin-right: 0.15rem;
    border-radius: 3px;
    overflow: hidden;
    text-align: center;
    color: #fff;
    &.bk {
      background: #ff8686;
    }
    &.mx {
      background: #7ecff5;
    }
  }
  .p-desc-title {
    width: 5.8rem;
  }
  .p-desc-content {
    width: 5.4rem;
  }
  .p-lookmore {
    &:active {
      color: #999;
    }
  }
}
.icon-title {
  display: block;
  width: 0.06rem;
  height: 0.34rem;
  margin-right: 0.15rem;
  background: url(../../assets/img/common/doc.png) no-repeat center;
}
.icon-q {
  display: block;
  width: 0.24rem;
  height: 0.24rem;
  margin-right: 0.15rem;
  background: url(../../assets/img/common/icon-list.png) no-repeat center;
  background-size: contain;
}
.icon-play{
  display: block;
  width: 0.8rem;
  height: 0.8rem;
  background: url(http://m.meb.com/Content/images/project/video1.png) no-repeat center;
  background-size: contain;
}
.wrap-box {
  .border-bottom {
    border-bottom: 1px solid #f2f2f2;
  }
  // 推荐商品
  .recommend-product {
    .product-img {
      width: 2.2rem;
      height: 2.2rem;
      margin-right: 0.3rem;
      border-radius: 0.16rem;
      background: #f5f5f5;
      overflow: hidden;
      img {
        display: block;
        border-radius: 0.16rem;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .recommend-desc {
      width: 4.4rem;
      height: 2.2rem;
      overflow: hidden;
      align-items: flex-start;
      justify-content: space-between;
      .product-name {
        color: #000000;
        font-family: NotoSansHans-Medium;
        font-weight: 500;
        font-size: 0.34rem;
        height: 1rem;
        width: 100%;
        line-height: 0.5rem;
      }
      .product-desc {
        width: 100%;
      }
      .product-price {
        width: 100%;
      }
    }
  }
  // 常见问题
  .question {
    .question-data {
      .data-number {
        flex: 1;
        padding-right: 0.2rem;
      }
      .meb-recommend {
        img {
          width: 0.32rem;
          height: 0.32rem;
          margin-right: 0.1rem;
        }
      }
    }
  }
  // 攻略
  .strategy {
    .strategy-title {
      width: 6.2rem;
    }
  }
  //案例
  .dairy{
    .user-head{
      display:block;
      width:.88rem;
      height:.88rem;
      object-fit: cover;
      border-radius: 50%;
      margin-right:.2rem;
    }
    .user-msg{
      display:block;
      width:calc( 100% - 1.2rem);
      min-height: .88rem;
      .diary-tags{
        .tags{
          padding:0 .1rem;
          background:#f3f3f3;
          border-radius: .06rem;
        }
      }
    }
    .diary-effect{
      .effect{
        position:relative;
        top:0;
        left:0;
        width:3.3rem;
        height:3.3rem;
        border-radius: .08rem;
        overflow: hidden;
        img{
          display:block;
          width:100%;
          height:100%;
          object-fit: cover;
          border-radius: .08rem;
        }
        span{
          position:absolute;
          left:0;
          bottom:0;
          padding:.05rem .18rem;
          border-top-right-radius: .16rem;
          border-bottom-left-radius: .1rem;
          color:#fff;
        }
        &.before{
          span{
            background:#0c0908
          }
        }
        &.after{
          span{
            background:#e1a993
          }
        }
      }
    }
  }
  //视频
  .video{
    .video-cover{
      width:100%;
      height:3.8rem;
      position:relative;
      top:0;
      left:0;
      border-radius: .08rem;
      overflow: hidden;
      &::before{
        content:"";
        display:block;
        position:absolute;
        z-index:10;
        top:0;
        left:0;
        width:100%;
        height:100%;
        background:#333;
        opacity: .5;
      }
      img{
        width:100%;
        height:100%;
        object-fit: cover;
        filter: grayscale(50%);
      }
      .play-btn{
        position:absolute;
        z-index:11;
        top:50%;
        left:50%;
        margin-left:-.4rem;
        margin-top:-.4rem;
      }
      .video-title{
        width:6.5rem;
        position:absolute;
        z-index:11;
        bottom:.2rem;
        left:50%;
        margin-left:-3.25rem;
        background:#fff;
        text-align: center;
        padding:.1rem;
        border-radius: 1rem;
        opacity: .9;
      }
    }
    
  }
  // 查看更多
  .look-more {
    img {
      width: 0.4rem;
      height: 0.4rem;
      margin-right: 0.12rem;
    }
  }
}
</style>


