<template>
  <m-container>
    <m-header headType="3"></m-header>
    <m-center :pageStatus="pageStatus">
      <div class="m-content animated fadeIn" v-cloak>
        <div class="bg-f5 p-b-20"></div>
        <ul class="zs-container">
            <router-link tag="li" class="zs-project flex flex-between border-bottom p-t-20 p-b-20 p-l-30 p-r-30"
                v-for="(project,i) in projectList" :key="i"
                :to="{path:project.Url}"
                >
                <img class="project-img" :src="host+project.ImageUrl" alt="">
                <div class="project-info flex flex-column flex-top flex-center">
                    <p class="c-33 font-32 one-line">{{project.Name}}</p>
                    <p class="c-b2 font-28 m-t-20 two-line">{{project.Desc}}</p>
                </div>
            </router-link>
        </ul>
      </div>
    </m-center>
  </m-container>
</template>

<script>
//import api from '@/plus/api';
import store from "@/store";
import config from "@/plus/http/config";
export default {
  name: "zs.index",
  data() {
    return {
      footerType: 0,
      pageStatus: 0,
      host: config.host,
      tabH:0,

      projectList:[
          {Name:'眼部整形',Desc:'迷人电眼，你我都可轻松拥有',ImageUrl:'/Content/images/zs/eye.png',Url:'/zs/ybzx/'},
          {Name:'鼻部整形',Desc:'挺拔秀丽的鼻子，触手可及'  ,ImageUrl:'/Content/images/zs/bi.png',Url:'/zs/bbzx/'},
          {Name:'胸部整形',Desc:'完美胸形，只需一招搞定',ImageUrl:'/Content/images/zs/chest.png',Url:'/zs/xbzx/'},
          {Name:'面部轮廓',Desc:'你想要的脸型都可成真',ImageUrl:'/Content/images/zs/facial.png',Url:'/zs/mblk/'},
          {Name:'脂肪整形',Desc:'只要想瘦，就有办法瘦下来',ImageUrl:'/Content/images/zs/thin.png',Url:'/zs/zfzx/'},
          {Name:'皮肤美容',Desc:'年轻态还是要靠皮肤呈现',ImageUrl:'/Content/images/zs/face.png',Url:'/zs/pfmr/'},
          {Name:'眉部整形',Desc:'年轻态还是要靠皮肤呈现',ImageUrl:'/Content/images/zs/mei.png',Url:'/zs/mbzx/'},
          {Name:'毛发美容',Desc:'毛发问题快速解决',ImageUrl:'/Content/images/zs/hair.png',Url:'/zs/mfmr/'},
          {Name:'口唇整形',Desc:'朱唇不够美，还要更美',ImageUrl:'/Content/images/zs/zui.png',Url:'/zs/kczx/'},
          {Name:'牙齿整形',Desc:'微笑的力量不要毁于牙齿',ImageUrl:'/Content/images/zs/ya.png',Url:'/zs/yczx/'},
          {Name:'耳部整形',Desc:'正常的耳形，不再是梦',ImageUrl:'/Content/images/zs/ear.png',Url:'/zs/ebzx/'},
          {Name:'私密整形',Desc:'只是想要更爱更幸福一点',ImageUrl:'/Content/images/zs/secret.png',Url:'/zs/smzx/'},
      ]
    };
  },
  computed: {
    
  },
  created(){
    // var SpellName = this.$route.params.spellname;
    // console.log(SpellName);
  },
  activated() {},
  deactivated() {},
  mounted() {
    this.pageStatus = 2;
    this.adJustStyle();
    this.getProject();
  },
  methods: {
    adJustStyle(){
      var bodyH = document.body.clientHeight;
      var headH = document.querySelector('.m-header').clientHeight;
      var titleH = document.querySelector('.title').offsetHeight;
      this.tabH = bodyH - headH - titleH;
    },
  }
};
</script>

<style lang="less" scoped>
.border-bottom{
    border-bottom:1px solid #f0f0f0;
}
.zs-container{
    .zs-project{
        .project-img{
            display:block;
            width:1.4rem;
            height:1.4rem;
            margin-right:.3rem;
        }
        .project-info{
            width:5.2rem;
            height:1.4rem;
        }
    }
}
</style>


