                <p class="p-t-30 font-28 c-99 l-h-42">口角上提手术目前只适用于静止状态下口角下垂者。无论外切法与内切法，其主要原理都是埋置悬吊线，将口角向上提升。外切法需要在口角上下方红白唇交界处做切口，切除口角上方适量皮肤，主要适用于上唇外侧皮肤比较松垂因素共同参与的口角下垂，相对内切法效果更明显一些，但目前观察口角处可能会留下轻微的切口疤痕痕迹。内切法在口内口角位置黏膜面做切口，口角不留痕迹，但相对来说上提效果较轻微。</p>
