<script>
//import api from '@/plus/api';
import store from "@/store";
import config from "@/plus/http/config";
export default {
  name: "project.detail",
  data() {
    return {
      pageStatus: 0,
      host: config.host,
      navIndex:0,
      //轮播nav
      swiperOptionNav: {
        slidesPerView: 'auto',
        spaceBetween: 30,
        freeMode: false,
        initialSlide: 0,
      },
      //轮播tab
      swiperOptionTab: {
        spaceBetween: 20,
        autoHeight: true,
        slidesPerView: 1,
        initialSlide: 0,
        on:{
          slideChange: ()=>{
            let swiperThumbs = this.$refs.swiperTab.swiper;
            this.navIndex = swiperThumbs.activeIndex;
          }
        },
      },

      //相关问题和相关内容
      tapRelative:0,


      zsId: "",
      isShowAll: false, //是否展示完
      detailData: void 0 //详情数据
    };
  },
  computed: {
    swiperTop(){
      return this.$refs.swiperNav.swiper;
    },
    swiperThumbs(){
      return this.$refs.swiperTab.swiper;
    },
  },
  created() {
    var id = this.$route.params.id;
    this.zsId = id;
    this.getDetail();
  },
  activated() {},
  deactivated() {},
  mounted() {
    this.swiper();
  },
  filters: {
    viewCount(value) {
      value = parseInt(value * 0.3);
      return value;
    },
    //时间差
    timeFormat(time) {
      var _this = this;
      var timeTip;
      time = (typeof time === 'number') ? new Date(time) : new Date((time).replace(/-/g, '/'));
      var nowTime = new Date(); //获取当前时间
      var timeDifference = time.getTime() - nowTime.getTime(); //时间差的毫秒数
      var day = 0,
        hour = 0,
        minute = 0,
        second = 0;
      if (timeDifference < 0) {
        timeDifference = Math.abs(timeDifference);
        day = Math.floor(timeDifference / 1000 / 60 / 60 / 24);
        hour = Math.floor((timeDifference / 1000 / 60 / 60) % 24);
        minute = Math.floor((timeDifference / 1000 / 60) % 60);
        second = Math.floor((timeDifference / 1000) % 60);
        timeTip =
          day > 0
            ? day + "天前"
            : hour > 0
            ? hour + "小时前"
            : minute > 0
            ? minute + "分钟前"
            : second > 0
            ? second + "秒前"
            : "";
        return timeTip;
      } else {
        day = Math.floor(timeDifference / 1000 / 60 / 60 / 24);
        hour = Math.floor((timeDifference / 1000 / 60 / 60) % 24);
        minute = Math.floor((timeDifference / 1000 / 60) % 60);
        second = Math.floor((timeDifference / 1000) % 60);
        timeTip =
          day > 0
            ? day + "天后"
            : hour > 0
            ? hour + "小时后"
            : minute > 0
            ? minute + "分钟后"
            : second > 0
            ? second + "秒后"
            : "";
        return timeTip;
      }
    },
  },
  methods: {
    //swiper-双向控制
    slidTo(index){
      event.preventDefault();
      this.navIndex = index;
      this.swiperThumbs.slideTo(this.navIndex, 200, false);
    },
    swiper(){
      this.$nextTick(() => {
        // this.swiperTop.controller.control = this.swiperThumbs
        this.swiperThumbs.controller.control = this.swiperTop
      })
      this.swiperThumbs.on('transitionEnd',() => {
        //this.swiperThumbs.slideTo(this.navIndex, 100, false);
        this.swiperThumbs.activeIndex = this.navIndex;
      })
    },
    //获取最新攻略
    async getDetail() {
      var _this = this;
      var model = {
        stateShowId: _this.zsId
      };
      await _this.$http.zs
        .getNewsDetail(model)
        .then(res => {
          if (res.data.Success) {
            _this.pageStatus = 2;
            _this.detailData = res.data.Content;
          }else{
            _this.$toast.center(res.data.Message);
            setTimeout(()=>{
              _this.$router.go(-1);
              location.reload();
            },1000)
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

