}
.border-top {
  border-top: 1px solid #ddd;
}
.content-wrap {
  &.limit-height {
    height: 4rem;
  }
  position: relative;
  top: 0;
  left: 0;
  height: auto;
  overflow: hidden;
  .look-more {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    background-color: #fff;
    padding: 0.2rem 0;
    text-align: center;
    box-shadow: 0 -0.5rem 1rem 0.1rem rgba(255, 255, 255, 0.8);
    .icon-arrowdown {
      display: block;
      width: 0.3rem;
      height: 0.3rem;
      position: relative;
      &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 0.2rem;
        height: 0.2rem;
        box-sizing: border-box;
        border-right: 0.02rem solid #999;
        border-bottom: 0.02rem solid #999;
        transform: rotate(45deg);
        padding: 0;
        margin-left: 0.05rem;
      }
    }
  }
}
.content-text {
  .content-desc {
    img {
      width: 100%;
      display: block;
      margin-bottom: 0.2rem;
      margin-top: 0.2rem;
    }
    p {
      font-size: 0.3rem;
      color: #666;
    }
  }
}
//相关项目
.relative-project{
  .title{
    width:100%;
    height:.8rem;
    line-height: .8rem;
    background:url(https://m.meb.com/Content/images/project/project-bg.png)no-repeat center;
    background-size:cover;
    text-align: center;
  }
  .relative-swiper{
    .scroll-view{
      touch-action: none;
    }
    .swiper-nav{
      .swiper-slide{
        width: auto !important;
        .nav-tag{
          color:#999;
          &.active{
            color:#000;
            position:relative;
            &::after{
              content:"";
              position:absolute;
              bottom:-.1rem;
              left:0;
              width:100%;
              height:.06rem;
              border-radius: .08rem;
              background:#ff8686;
            }
          }
        }
      }
    }
    .swiper-tab{
      .swiper-slide{
        border-radius: .16rem;
        box-shadow: 0 1px 10px 0 rgba(204, 204, 204, 0.5), 0 1px 8px 0 rgba(204, 204, 204, 0.2);
        overflow: hidden;
        padding:.3rem;
        transition: 300ms;
        transform: scale(0.9);
        &.swiper-slide-active{
          transform: scale(1);
        }
        .learn-more{
          display:block;
          padding:.1rem .2rem;
          border-radius: 1rem;
          background:#f5f5f5;
          text-align: center;
        }
      }
    }
  }
  
}
//相关问题和内容
.relative-content{
  .title-box{
    .title{
      flex:1;
      flex-shrink: 0;
      text-align: center;
      position:relative;
      padding-bottom:.1rem;
      &:after{
        content:"";
        position:absolute;
        top:0;
        right:0;
        width:1px;
        height:100%;
        background:#f0f0f0;
      }
      &:last-child:after{
        background:transparent;
      }
      &.active{
        font-weight: bold;
        color:#000;
        position: relative;
        &:before{
          content:"";
          position: absolute;
          left:50%;
          bottom:0;
          width:.6rem;
          height:.08rem;
          margin-left:-.3rem;
          border-radius: .08rem;
          background:#ff8686;
        }
      }
    }
  }
  .content-boxs{
    .relative-list{
      .border-bottom{
        border-bottom:1px solid #f0f0f0;
      }
      .content-item{
        overflow: hidden;
        .content-item-left{
          width:4.4rem;
          height:1.4rem;
        }
        .content-item-right{
          width:2.2rem;
          height:1.4rem;
          margin-left:.3rem;
        }
      }
    }
  }
}
//医院
.hospital{
  .hospital-head{
    width:1.28rem;
    height:1.28rem;
    border-radius: .08rem;
    object-fit:cover;
  }
  .hospital-info{
    width:5.3rem;
    height:1.28rem;
    margin-left:.3rem;
    p{
      width:100%;
    }
  }
}
// 查看更多
.look-more{
  img{
    width:.4rem;
    height:.4rem;
    margin-right:.12rem;
  }
}
</style>
<style lang="css">
.content-wrap p{
  font-size: 0.3rem;
  color: #666;
  line-height: .48rem;
}
.content-wrap p span{
  font-size: 0.3rem;
  color: #666;
  line-height: .48rem;
}
.content-wrap img{
  margin:.1rem 0;
}

</style>
<style lang="css" scoped>

</style>


