<template>
  <m-container>
    <m-header headType="3"></m-header>
    <m-center :pageStatus="pageStatus">
      <!-- 服务 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30" v-cloak>
        <div class="server-box flex flex-wrap">
          <div class="server-item m-b-30">
            <img class="server-bg" src="https://m.meb.com/Content/images/question/free.png" alt="">
            <div class="server flex flex-column flex-between flex-top p-t-30 p-l-30 p-r-30 p-b-20">
              <p class="flex font-34 c-ff"><i class="icon-server icon-server-free"></i>免费咨询</p>
              <p class="font-24 two-line c-ff">一万名美呗签约专业医生解答</p>
            </div>
          </div>
          <div class="server-item m-b-30">
            <img class="server-bg" src="https://m.meb.com/Content/images/question/tel.png" alt="">
            <div class="server flex flex-column flex-between flex-top p-t-30 p-l-30 p-r-30 p-b-20">
              <p class="flex font-34 c-ff"><i class="icon-server icon-server-tel"></i>电话咨询</p>
              <p class="font-24 two-line c-ff">拨打电话:400-105-8500每天9点 - 21点 </p>
            </div>
          </div>
          <div class="server-item">
            <img class="server-bg" src="https://m.meb.com/Content/images/question/appiont.png" alt="">
            <div class="server flex flex-column flex-between flex-top p-t-30 p-l-30 p-r-30 p-b-20">
              <p class="flex font-34 c-ff"><i class="icon-server icon-server-appiont"></i>预约专家</p>
              <p class="font-24 two-line c-ff">10秒极速接待，专业美容顾问帮你约专家</p>
            </div>
          </div>
          <div class="server-item">
            <img class="server-bg" src="https://m.meb.com/Content/images/question/car.png" alt="">
            <div class="server flex flex-column flex-between flex-top p-t-30 p-l-30 p-r-30 p-b-20">
              <p class="flex font-34 c-ff"><i class="icon-server icon-server-cat"></i>医院直通车</p>
              <p class="font-24 two-line c-ff">直连医院整形科室，专业医生一对一服务</p>
            </div>
          </div>
        </div>
      </div>
      <div class="bg-f5 p-b-20"></div>
      <!-- 热点问题分类 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30">
        <div class="box-title flex">
          <p class="font-36 bold">热点问题分类</p>
        </div>
        <div class="hot-question wrap-box p-t-30">
          <div class="hot-question-box flex flex-wrap">
            <p class="hot-question-tag font-30 one-line">注射物/假</p>
            <p class="hot-question-tag font-30 one-line">眼部修复</p>
            <p class="hot-question-tag font-30 one-line">垫鼻基底</p>
            <p class="hot-question-tag font-30 one-line">乳头内陷</p>
            <p class="hot-question-tag font-30 one-line">磨骨整形</p>
            <p class="hot-question-tag font-30 one-line">瘦大腿</p>
            <p class="hot-question-tag font-30 one-line">祛痣</p>
            <p class="hot-question-tag font-30 one-line">仪器抗衰</p>
          </div>
          <router-link to tag="div" class="look-more flex flex-center p-t-30">
            <img src="../../assets/img/common/more.png" alt />
            <span class="font-28 c-99">查看全部问题</span>
          </router-link>
        </div>
      </div>
      <div class="bg-f5 p-b-20"></div>
      
      <!-- 最新问题 -->
      <div class="wrap p-t-30 p-l-30 p-r-30 p-b-30" v-cloak v-if="newsList">
        <div class="box-title flex">
          <p class="font-36 bold">最新问题</p>
        </div>
        <div class="wrap-box new-question-box">
          <router-link :to="{}" tag="div" 
            class="question border-bottom p-t-30 p-b-30"
            v-for="(news,index) in newsList" :key="index">
            <p class="c-33 font-32 flex flex-top">
              <i class="icon-question"></i>
              <span class="question-title two-line font-32 c-33">吸脂减肥的费用？</span>
            </p>
            <p class="p-t-20 font-32 flex flex-top">
              <i class="icon-answer"></i>
              <span class="question-title three-line font-32 c-99">
                吸脂减肥的费用10000-30000元左右，但费用不是固定的，它和多方面的因素都有关，具体有以下几点：1、吸脂减肥与求美者自身情况有关：每个人的肥胖程度情况是不一样的，所以要根据每个人的个性化的需求来决定价格的多少。2、吸脂减肥与手术方式有关：吸脂减肥的手术方法目前比较常用的有负压吸脂、水动力吸脂等等。不同的吸脂手术其费用自然也就有所不同，不同的求美者由于身体体质、生理构造等方面的不同，不同不美的吸脂价格也不同。3、吸脂减肥与选择的医院医生有关：大型正规医院的价格一般都要高于小规模的医疗机构，专家级医生的手术费用一般要高于普通医生，拥有高科技设施的医疗机构一般医疗水平都比较先进，因此价格上也会较高，但好的医疗设施、专业的医生、正规的医院也是保证手术效果的关键因素。
              </span>
            </p>
          </router-link>
          <router-link to tag="div" class="look-more flex flex-center p-t-30">
            <img src="../../assets/img/common/more.png" alt />
            <span class="font-28 c-99">查看更多问题</span>
          </router-link>
        </div>
      </div>
    </m-center>
  </m-container>
</template>

<script>
//import api from '@/plus/api';
import store from "@/store";
import config from "@/plus/http/config";
export default {
  name: "question.index",
  data() {
    return {
      footerType: 0,
      pageStatus: 0,
      host: config.host,
      
      newsList:void 0,//最新攻略列表
    };
  },
  computed: {},
  created() {
    this.getNews();
  },
  activated() {},
  deactivated() {},
  mounted() {
    this.pageStatus = 2;
  },
  methods: {
    //获取最新攻略
    async getNews(){
      var _this = this;
      await _this.$http.news.getData().then(res => {
        if(res.data.Success){
          _this.newsList = res.data.Content;
        }
      }).catch(err=>{
        console.log(err)
      })
    }
  }
};
</script>

<style lang="less" scoped>
.icon-title {
  display: block;
  width: 0.06rem;
  height: 0.34rem;
  margin-right: 0.15rem;
  background: url(../../assets/img/common/doc.png) no-repeat center;
}
.icon-server{
  display: block;
  width: 0.38rem;
  height: 0.38rem;
  margin-right: 0.15rem;
}
.icon-server-free{
  background: url(https://m.meb.com/Content/images/question/free-zx.png) no-repeat center;
  background-size: contain;
}
.icon-server-tel{
  background: url(https://m.meb.com/Content/images/question/tel-zx.png) no-repeat center;
  background-size: contain;
}
.icon-server-appiont{
  background: url(https://m.meb.com/Content/images/question/appiont-zx.png) no-repeat center;
  background-size: contain;
}
.icon-server-cat{
  background: url(https://m.meb.com/Content/images/question/cat-zx.png) no-repeat center;
  background-size: contain;
}
.icon-question{
  display: block;
  flex-shrink: 0;
  width: 0.4rem;
  height: 0.4rem;
  margin-right: 0.15rem;
  background: url(https://m.meb.com/Content/images/question/now-wen.png) no-repeat center;
  background-size: contain;
}
.icon-answer{
  display: block;
  flex-shrink: 0;
  width: 0.4rem;
  height: 0.4rem;
  margin-right: 0.15rem;
  background: url(https://m.meb.com/Content/images/question/now-da.png) no-repeat center;
  background-size: contain;
}
//服务
.server-box{
  .server-item{
    width:3.3rem;
    height:1.75rem;
    border-radius:.08rem;
    overflow: hidden;
    margin-right:.3rem;
    position: relative;
    &:nth-child(2n){
      margin-right:0;
    }
    .server-bg{
      position:absolute;
      left:0;
      top:0;
      right:0;
      bottom:0;
    }
    .server{
      position:relative;
      z-index:1;
      width:100%;
      height:100%;
    }
  }
}
//热门问题
.hot-question{
  .hot-question-box{
    .hot-question-tag{
      width:calc( (100% - .6rem)/4);
      text-align: center;
      margin-right:.2rem;
      margin-bottom:.2rem;
      padding:.05rem 0;
      background:#fff2ed;
      border-radius: .05rem;
      color:#666;
      &:nth-child(4n){
        margin-right:0;
      }
    }
  }
}
.wrap-box {
  .border-bottom {
    border-bottom: 1px solid #f2f2f2;
  }
  //最新问题
  .new-question-box{
    .question{

    }
  }
  // 查看更多
  .look-more {
    img {
      width: 0.4rem;
      height: 0.4rem;
      margin-right: 0.12rem;
    }
  }
}
</style>


